import React from "react";
import { IconButton } from "@mui/material/";
import makeStyles from "@mui/styles/makeStyles";
import CheckOutlinedIcon from "@mui/icons-material/CheckOutlined";
import ErrorOutlineOutlinedIcon from "@mui/icons-material/ErrorOutlineOutlined";
import ShareIcon from "@mui/icons-material/Share";
import WarningAmberOutlinedIcon from "@mui/icons-material/WarningAmberOutlined";
import styles from "./styles";

const AssetStatusIcon = ({ data, isLinked }) => {
    const { isAssigned, isExpected, isMandatory, isRelated } = data;
    const classes = makeStyles(styles)();
    let iconStyle = "";
    let IconType = <CheckOutlinedIcon />;

    if (isAssigned) {
        IconType = <CheckOutlinedIcon />;
        iconStyle = classes.searchAssigned;
    } else if (!isRelated) {
        IconType = <ErrorOutlineOutlinedIcon />;
        iconStyle = classes.searchNotRelated;
    } else if (isMandatory && isLinked) {
        IconType = <ShareIcon />;
        iconStyle = classes.searchLinked;
    } else if (isMandatory && !isExpected) {
        IconType = <WarningAmberOutlinedIcon />;
        iconStyle = classes.searchNotExpected;
    } else if (!isMandatory) {
        IconType = <ErrorOutlineOutlinedIcon />;
        iconStyle = classes.searchNotMandatory;
    }

    return (
        <IconButton role="button" className={iconStyle}>
            {IconType}
        </IconButton>
    );
};

export default AssetStatusIcon;
