import React from "react";
import { useParams } from "react-router-dom";
import { useAssetTypeIdentifiers } from "../../common/hooks";
import { useTranslation } from "react-i18next";
import useAuditAsset from "../../common/hooks/useAuditAsset";
import makeStyles from "@mui/styles/makeStyles";
import styles from "./styles";

import { Box, Grid, Typography } from "@mui/material/";

import { useTypesListAssetTypes, useTypesListMake } from "../../common/hooks";
/**
 * Audit Asset Attributes Component
 */
export default () => {
    const classes = makeStyles(styles)();
    const { t } = useTranslation();
    const { assetId } = useParams();
    const asset = useAuditAsset(assetId);
    const { assetCore } = asset;

    const identifiers = useAssetTypeIdentifiers(assetCore.type);
    const type = useTypesListAssetTypes(assetCore.type);
    const make = useTypesListMake(assetCore.make);

    return (
        <Grid container className={classes.padding}>
            <Grid item xs={12} sm container>
                <Grid item xs={6}>
                    <Typography variant="subtitle1" component="div">
                        <Box component="span" fontWeight="fontWeightMedium">
                            {identifiers.identifier1}
                        </Box>
                    </Typography>
                    <Typography variant="body2" gutterBottom>
                        {assetCore.identifier1}
                    </Typography>
                </Grid>
                {Boolean(assetCore.identifier2) && (
                    <Grid item xs={6}>
                        <Typography variant="subtitle1" component="div">
                            <Box component="span" fontWeight="fontWeightMedium">
                                {identifiers.identifier2}
                            </Box>
                        </Typography>
                        <Typography variant="body2" gutterBottom>
                            {assetCore.identifier2}
                        </Typography>
                    </Grid>
                )}
            </Grid>
            {Boolean(assetCore.identifier3 || assetCore.identifier4) && (
                <Grid item xs={12} sm container>
                    {Boolean(assetCore.identifier3) && (
                        <Grid item xs={6}>
                            <Typography variant="subtitle1" component="div">
                                <Box
                                    component="span"
                                    fontWeight="fontWeightMedium"
                                >
                                    {identifiers.identifier3}
                                </Box>
                            </Typography>
                            <Typography variant="body2" gutterBottom>
                                {assetCore.identifier3}
                            </Typography>
                        </Grid>
                    )}
                    {Boolean(assetCore.identifier4) && (
                        <Grid item xs={6}>
                            <Typography variant="subtitle1" component="div">
                                <Box
                                    component="span"
                                    fontWeight="fontWeightMedium"
                                >
                                    {identifiers.identifier4}
                                </Box>
                            </Typography>
                            <Typography variant="body2" gutterBottom>
                                {assetCore.identifier4}
                            </Typography>
                        </Grid>
                    )}
                </Grid>
            )}
            <Grid item xs={12} sm container>
                <Grid item xs={6}>
                    <Typography variant="subtitle1" component="div">
                        <Box component="span" fontWeight="fontWeightMedium">
                            {t("make_one")}
                        </Box>
                    </Typography>
                    <Typography variant="body2" gutterBottom>
                        {make.name}
                    </Typography>
                </Grid>
                <Grid item xs={6}>
                    <Typography variant="subtitle1" component="div">
                        <Box component="span" fontWeight="fontWeightMedium">
                            {t("type")}
                        </Box>
                    </Typography>
                    <Typography variant="body2" gutterBottom>
                        {type.name}
                    </Typography>
                </Grid>
            </Grid>
        </Grid>
    );
};
