import React, { useState } from "react";
import {
    AppBar,
    Divider,
    Grid,
    IconButton,
    Toolbar,
    Typography,
} from "@mui/material/";
import { Menu, Close } from "@mui/icons-material";
import { useNavigate, useLocation } from "react-router-dom";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import makeStyles from "@mui/styles/makeStyles";
import Drawer from "../../common/components/Drawer";
import Logo from "../../common/images/logo-light.png";
import styles from "./menuStyles";
import { useTranslation } from "react-i18next";
import menuUtils from "./menuUtils";
import useMenuItems from "./useMenuItems";

/**
 * App UserMenu Component
 *
 */
export default () => {
    const classes = makeStyles(styles)();
    const { t } = useTranslation();
    const location = useLocation();
    const navigate = useNavigate();
    const { pathname } = location;
    const [open, setOpen] = useState(false);
    const pathHasNumbers = menuUtils.containsNumbers(pathname);
    const title = menuUtils.screenTitle(pathname);
    const items = useMenuItems(title);

    const toggleDrawer = () => {
        setOpen(!open);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };
    const goBack = () => {
        handleDrawerClose();
        navigate(-1);
    };

    return (
        <>
            <AppBar
                className={classes.appBar}
                elevation={0}
                position="sticky"
                sx={{ top: 0 }}
                color="primary"
            >
                <Toolbar className={classes.toolbar}>
                    <Grid container>
                        <Grid item xs={3} className={classes.menuLeftContainer}>
                            {pathHasNumbers === true ? (
                                <>
                                    <IconButton onClick={goBack}>
                                        <ArrowBackIcon color="white" />
                                    </IconButton>
                                </>
                            ) : (
                                <>
                                    <Typography
                                        component="img"
                                        src={Logo}
                                        alt="CheckVentory logo"
                                        className={classes.logoContent}
                                    ></Typography>
                                </>
                            )}
                        </Grid>
                        <Grid item xs={6} className={classes.titleText}>
                            {pathHasNumbers === true ? (
                                <Typography component="h5">
                                    {t(`titles.${title}`)}
                                </Typography>
                            ) : null}
                        </Grid>
                        <Grid item xs={3}>
                            <IconButton
                                className={classes.iconButton}
                                onClick={toggleDrawer}
                                size="large"
                            >
                                {!open ? <Menu /> : <Close />}
                            </IconButton>
                        </Grid>
                    </Grid>
                </Toolbar>
                <Divider />
            </AppBar>
            <Drawer
                disabled={true}
                items={items}
                onClose={handleDrawerClose}
                open={open}
            />
        </>
    );
};
