import { actionTypes } from './actions';

const initialState = {
  total: 0,
  uploaded: 0,
};

export default function (state = initialState, action) {
  switch (action.type) {
    case actionTypes.AUDIT_UPLOAD_STATUS_SET: {
      const { status } = action.payload;
      return status;
    }
    case actionTypes.AUDIT_UPLOAD_STATUS_UPLOADED: {
      const { uploaded } = action.payload;
      return { ...state, uploaded: state.uploaded + uploaded };
    }
    default: {
      return state;
    }
  }
}
