import { all } from "redux-saga/effects";
import altAssets from "./alt-assets/saga";
import assetAuditors from "./asset-auditors/saga";
import assetSync from "./asset-sync/saga";
import auditAssets from "./audit-assets/saga";
import auditAssignments from "./audit-assignments/saga";
import auditCompletion from "./audit-completion/saga";
import auditDownload from "./audit-download/saga";
import auditLocations from "./audit-locations/saga";
import auditUpload from "./audit-upload/saga";
import audits from "./audits/saga";
import auth from "./auth/saga";
import geoLocation from "./geo-location/saga";
import http from "./http/saga";
import typesList from "./types-list/saga";

/**
 * Root Saga
 */
export default function* () {
    yield all([
        altAssets,
        assetAuditors,
        assetSync,
        auditAssets,
        auditAssignments,
        auditCompletion,
        auditDownload,
        auditLocations,
        auditUpload,
        audits,
        auth,
        geoLocation,
        http,
        typesList,
    ]);
}
