import { combineReducers } from "redux";
import altAssets from "./alt-assets/reducer";
import app from "./app/reducer";
import appSettings from "./app-settings/reducer";
import assetAuditors from "./asset-auditors/reducer";
import auditAssets from "./audit-assets/reducer";
import auditAssignments from "./audit-assignments/reducer";
import auditCompletion from "./audit-completion/reducer";
import auditLocations from "./audit-locations/reducer";
import audits from "./audits/reducer";
import auditsStarted from "./audits-started/reducer";
import auditUpload from "./audit-upload/reducer";
import auth from "./auth/reducer";
import geoLocation from "./geo-location/reducer";
import http from "./http/reducer";
import toast from "./toast/reducer";
import typesList from "./types-list/reducer";

/**
 * Root Reducer
 */
export default combineReducers({
    altAssets,
    app,
    appSettings,
    assetAuditors,
    auditAssets,
    auditAssignments,
    auditCompletion,
    auditLocations,
    audits,
    auditsStarted,
    auditUpload,
    auth,
    geoLocation,
    http,
    toast,
    typesList,
});
