import { ERROR_ACTION, WAIT_FOR_ACTION } from 'redux-wait-for-action';

export const actionTypes = {
  AUDIT_DOWNLOAD_GET_FAILURE: 'AUDIT_DOWNLOAD_GET_FAILURE',
  AUDIT_DOWNLOAD_GET_REQUEST: 'AUDIT_DOWNLOAD_GET_REQUEST',
  AUDIT_DOWNLOAD_GET_SUCCESS: 'AUDIT_DOWNLOAD_GET_SUCCESS',
};

export const actionCreators = {
  /**
   * Audit Download API GET
   */
  getRequest: () => ({
    type: actionTypes.AUDIT_DOWNLOAD_GET_REQUEST,
    [WAIT_FOR_ACTION]: actionTypes.AUDIT_DOWNLOAD_GET_SUCCESS,
    [ERROR_ACTION]: actionTypes.AUDIT_DOWNLOAD_GET_FAILURE,
  }),
  getSuccess: (audits) => ({
    type: actionTypes.AUDIT_DOWNLOAD_GET_SUCCESS,
    payload: { audits },
  }),
  getFailure: (error) => ({
    type: actionTypes.AUDIT_DOWNLOAD_GET_FAILURE,
    error,
  }),
};

export default {
  create: actionCreators,
  type: actionTypes,
};
