import {
    all,
    call,
    cancel,
    delay,
    fork,
    put,
    take,
    takeLatest,
} from "redux-saga/effects";
import config from "../../config";
import { actionCreators, actionTypes } from "./actions";

const getCurrentPosition = (options) =>
    new Promise((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(resolve, reject, options);
    });

function* geoLocationGet() {
    try {
        //const hasPermission = yield call(hasLocationPermission);
        //if (hasPermission) -> left for reference
        const { coords } = yield call(getCurrentPosition, null);
        yield put(actionCreators.getSuccess(coords));
        // } else {
        //     yield put(actionCreators.getFailure("NO_PERMISSION"));
        // }
    } catch (error) {
        yield put(actionCreators.getFailure(error.toString()));
    }
}

function* geoLocationWatchTaskTick() {
    while (true) {
        yield delay(config.geoLocation.timeout);
        yield put(actionCreators.getRequest());
    }
}

function* geoLocationWatch() {
    while (yield take(actionTypes.GEO_LOCATION_WATCH_START)) {
        const workerTask = yield fork(geoLocationWatchTaskTick);
        yield take(actionTypes.GEO_LOCATION_WATCH_STOP);
        yield cancel(workerTask);
    }
}

export default (function* () {
    yield all([
        takeLatest(actionTypes.GEO_LOCATION_GET_REQUEST, geoLocationGet),
        geoLocationWatch(),
    ]);
})();
