import React, { useEffect, useState, useRef } from "react";
import makeStyles from "@mui/styles/makeStyles";
import styles from "./styles";

import { Grid, Box, IconButton, CircularProgress } from "@mui/material/";
import FlashAutoIcon from "@mui/icons-material/FlashAuto";
import PanoramaFishEyeIcon from "@mui/icons-material/PanoramaFishEye";
import { initializeMedia } from "./cameraUtils";

const TakePhoto = ({ onSuccess, overlay }) => {
    const [cameraNumber, setCameraNumber] = useState(0);
    const [cameraFlash, setCameraFlash] = useState(false);
    const [isCameraLoading, setIsCameraLoading] = useState(true);
    const player = useRef(null);
    const classes = makeStyles(styles)();

    useEffect(() => initializeMedia(player, setIsCameraLoading), []);

    const capturePicture = () => {
        let canvas = document.createElement("canvas");
        canvas.width = player.current.videoWidth;
        canvas.height = player.current.videoHeight;
        const height =
            player.current.videoHeight / (canvas.width / window.outerWidth);

        // left for debugging
        /*
        alert(
            `videoWidth/heights:${player.current.videoWidth},${player.current.videoHeight},${height}`
        );
        */

        let contex = canvas.getContext("2d");
        contex.drawImage(player.current, 0, 0, canvas.width, canvas.height);
        player.current.srcObject.getVideoTracks().forEach((track) => {
            track.stop();
        });

        onSuccess({
            uri: canvas.toDataURL(),
            width: canvas.width,
            height: height ? height : 0,
        });
    };

    const toggleFlash = () => {
        const track = player.current.srcObject.getVideoTracks()[0];
        const imageCapture = new ImageCapture(track);

        imageCapture.getPhotoCapabilities().then(() => {
            //TODO: check if camera has a torch
            track.applyConstraints({
                advanced: [{ torch: !cameraFlash }],
            });
        });

        setCameraFlash(!cameraFlash);
    };

    // left for future proof
    const switchCamera = async () => {
        const listOfVideoInputs = await getListOfVideoInputs();

        // The device has more than one camera
        if (listOfVideoInputs.length > 1) {
            if (player.current.srcObject) {
                player.current.srcObject.getVideoTracks().forEach((track) => {
                    track.stop();
                });
            }

            // switch to second camera
            if (cameraNumber === 0) {
                setCameraNumber(1);
            }
            // switch to first camera
            else if (cameraNumber === 1) {
                setCameraNumber(0);
            }

            // Restart based on camera input
            initializeMedia();
        } else if (listOfVideoInputs.length === 1) {
            alert("The device has only one camera");
        } else {
            alert("The device does not have a camera");
        }
    };

    return (
        <div className={classes.camera}>
            <div id="video_overlay" className={classes.videoOverlay}>
                <img
                    src={overlay}
                    alt="realtime-image"
                    className={classes.overlayImage}
                />
                <video
                    ref={player}
                    autoPlay
                    playsInline
                    className={classes.camera}
                ></video>
            </div>

            <Grid container className={classes.cameraButtons}>
                {isCameraLoading ? (
                    <Grid item xs={12}>
                        <Box className={classes.loadingWrapper}>
                            <CircularProgress />
                        </Box>
                    </Grid>
                ) : (
                    <>
                        <Grid item xs={3} className={classes.flash}>
                            <IconButton
                                color={cameraFlash ? "primary" : "secondary"}
                                aria-label="toggle flash"
                                component="span"
                                onClick={toggleFlash}
                            >
                                <FlashAutoIcon style={{ fontSize: "3rem" }} />
                            </IconButton>
                        </Grid>
                        <Grid item xs={6} className={classes.imageCapture}>
                            <IconButton
                                color="primary"
                                aria-label="capture image"
                                component="span"
                                onClick={capturePicture}
                            >
                                <PanoramaFishEyeIcon
                                    style={{ fontSize: "5rem" }}
                                />
                            </IconButton>
                        </Grid>
                        <Grid item xs={3}></Grid>
                    </>
                )}
            </Grid>
        </div>
    );
};

export default TakePhoto;
