import { useMemo } from "react";
import { useSelector } from "react-redux";

export default (auditId) => {
    const { altAssets, assetAuditors, auditAssets } = useSelector(
        (state) => state
    );

    const dataChecked = useMemo(() => {
        return [
            ...altAssets.data.filter(
                (item) => item.audit.id === parseInt(auditId)
            ),
            ...auditAssets.data
                .filter((item) => item.isAssigned === true)
                .map((item) => ({
                    ...item,
                    ...(assetAuditors.data.find(
                        (i) => i.auditAsset.id === item.id
                    ) || {}),
                })),
        ];
    }, [altAssets, assetAuditors, auditAssets]);

    return dataChecked;
};
