import { actionTypes } from "./actions";

export default function (state = {}, action) {
    switch (action.type) {
        case actionTypes.AUDITS_STARTED_CLEAR: {
            return {};
        }
        case actionTypes.AUDITS_STARTED_UPDATE: {
            const { auditsStarted } = action.payload;
            return { ...state, ...auditsStarted };
        }
        default: {
            return state;
        }
    }
}
