import React, { useEffect, useCallback } from "react";
import { Box } from "@mui/material/";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import * as Sentry from "@sentry/react";
import { useTranslation } from "react-i18next";
import { useToaster } from "../../common/hooks";
import actions from "../../Store/actions";
import Checked from "./Checked.js";
import Unchecked from "./Unchecked.js";
import useAudit from "../../common/hooks/useAudit";
import useDataUnChecked from "./useDataUnchecked";
import useDataChecked from "./useDataChecked";

const Assets = ({ assetListType }) => {
    const { auditId } = useParams();
    const { auditAssignments } = useSelector((state) => state);
    const audit = useAudit(auditId);
    const dispatch = useDispatch();
    const dataUnchecked = useDataUnChecked();
    const dataChecked = useDataChecked(auditId);
    const { t } = useTranslation();
    const toast = useToaster();

    useEffect(() => {
        onRefresh();
    }, [auditAssignments, audit]);

    const onRefresh = useCallback(() => {
        const selectData = async () => {
            try {
                if (auditAssignments && audit) {
                    const assignments = auditAssignments.data.filter(
                        (item) => item.auditLocation.audit.id === audit.id
                    );
                    //TODO: Make sure one code for assignments
                    const assignmentsIds = auditAssignments.data.reduce(
                        (accum, item) => {
                            if (item.auditLocation.audit.id === audit.id)
                                return [...accum, item.id];
                            return accum;
                        },
                        []
                    );

                    const locIds = assignments.map(
                        (item) => item.auditLocation.id
                    );

                    await Promise.all([
                        dispatch(
                            actions.altAssets.create.selectRequest(auditId)
                        ),
                        dispatch(
                            actions.assetAuditors.create.selectRequest(
                                assignmentsIds
                            )
                        ),
                        dispatch(
                            actions.auditAssets.create.selectRequest(locIds)
                        ),
                    ]);
                }
            } catch (error) {
                console.log(error);
                toast.error(t("errors.auditAssetsGet"));
                Sentry.captureException(error);
            }
        };
        selectData();
    }, [auditAssignments, audit]);

    return (
        <Box sx={{ typography: "body1" }}>
            {assetListType === "checked" ? (
                <Checked data={dataChecked}></Checked>
            ) : (
                <Unchecked data={dataUnchecked}></Unchecked>
            )}
        </Box>
    );
};

export default Assets;
