import React from "react";
import { useTranslation } from "react-i18next";
import makeStyles from "@mui/styles/makeStyles";
import styles from "./styles";

import ActionBar from "../../ActionBar";
import { Button, Grid } from "@mui/material/";
import SaveIcon from "@mui/icons-material/Save";
import PhotoCameraIcon from "@mui/icons-material/PhotoCamera";

type Props = {
    onRetakePhoto: Function,
    onSuccess: Function,
    photo: any,
};

/**
 * Save Photo Component
 */
export default ({ onRetakePhoto, onSuccess, photo }: Props) => {
    const onSavePhotoPress = () => onSuccess && onSuccess();
    const classes = makeStyles(styles)();
    const { t } = useTranslation();

    const RetakePhoto = () => (
        <Button
            variant="outlined"
            color="primary"
            fullWidth
            onClick={onRetakePhoto}
            endIcon={<PhotoCameraIcon />}
        >
            {t("retakePhoto")}
        </Button>
    );

    const SavePhoto = () => (
        <Button
            variant="contained"
            color="primary"
            fullWidth
            onClick={onSavePhotoPress}
            endIcon={<SaveIcon />}
        >
            {t("savePhoto")}
        </Button>
    );

    return (
        <>
            <div className={classes.previewWrp}>
                <img
                    src={photo.uri}
                    alt="photo"
                    className={classes.preview}
                    style={{
                        height: photo.height,
                    }}
                />
            </div>
            <ActionBar>
                <Grid container spacing={2}>
                    <Grid item xs={6}>
                        <RetakePhoto />
                    </Grid>
                    <Grid item xs={6}>
                        <SavePhoto />
                    </Grid>
                </Grid>
            </ActionBar>
        </>
    );
};
