import { ERROR_ACTION, WAIT_FOR_ACTION } from 'redux-wait-for-action';

export const actionTypes = {
  AUDIT_LOCATIONS_SELECT_FAILURE: 'AUDIT_LOCATIONS_SELECT_FAILURE',
  AUDIT_LOCATIONS_SELECT_REQUEST: 'AUDIT_LOCATIONS_SELECT_REQUEST',
  AUDIT_LOCATIONS_SELECT_SUCCESS: 'AUDIT_LOCATIONS_SELECT_SUCCESS',
};

export const actionCreators = {
  selectRequest: (query: string) => ({
    type: actionTypes.AUDIT_LOCATIONS_SELECT_REQUEST,
    payload: { query },
    [WAIT_FOR_ACTION]: actionTypes.AUDIT_LOCATIONS_SELECT_SUCCESS,
    [ERROR_ACTION]: actionTypes.AUDIT_LOCATIONS_SELECT_FAILURE,
  }),
  selectSuccess: (auditLocations: Array<any>) => ({
    type: actionTypes.AUDIT_LOCATIONS_SELECT_SUCCESS,
    payload: { auditLocations },
  }),
  selectFailure: (error: Error) => ({
    type: actionTypes.AUDIT_LOCATIONS_SELECT_FAILURE,
    error,
  }),
};

export default {
  create: actionCreators,
  type: actionTypes,
};
