import { all, call, put, takeLatest } from "redux-saga/effects";
import jwtDecode from "jwt-decode";
import { encode } from "base-64";
import moment from "moment";
import actions from "../actions";
import { http } from "../../common/lib";
import config from "../../config";
import { actionCreators, actionTypes } from "./actions";

function* get(action) {
    const { password, username } = action.payload;
    const authUrl = `${config.auth.url}?app_client_id=${config.auth.appClientId}`;
    try {
        const auth = yield call(http.anonymous.get, authUrl, {
            Authorization: `Basic ${encode(`${username}:${password}`)}`,
        });
        const { exp, sub } = jwtDecode(auth.data.idToken);
        const accountUrl = `${config.api.baseUrl}${config.api.paths.me}?identity_id=${sub}`;
        const account = yield call(http.anonymous.get, accountUrl, {
            Authorization: `JWT ${auth.data.idToken}`,
        });

        yield put(
            actionCreators.getSuccess({
                ...auth.data,
                account: account.data[0],
                expiryDate: moment.unix(exp).utc(),
                identityId: sub,
                username,
            })
        );

        const authString = JSON.stringify({
            ...auth.data,
            account: account.data[0],
            expiryDate: moment.unix(exp).utc(),
            identityId: sub,
            username,
        });
        // update storage with recent Authentication
        localStorage.setItem("persist:root", authString);
    } catch (error) {
        yield put(actionCreators.getFailure(error.toString()));
    }
}

function* getRegistrationRequest(action) {
    const { regData } = action.payload;
    const url = `${config.api.paths.createAccount}/${regData.token}`;

    try {
        const { data } = yield call(http.anonymous.get, url);

        yield put(actionCreators.getRegSuccess(data));
    } catch (error) {
        console.error(error);
        yield put(actionCreators.getRegFailure(error));
    }
}

function* postRegistrationRequest(action) {
    const { regData } = action.payload;
    const url = config.api.paths.createAccount;

    try {
        const { data } = yield call(http.authorized.post, url, regData);

        yield put(actionCreators.postRegSuccess(data));
        yield put(actions.appUI.create.setLoading({ REQUEST: false }));
    } catch (error) {
        console.error(error);
        yield put(actionCreators.postRegFailure(error));
        yield put(actions.appUI.create.setLoading({ REQUEST: false }));
    }
}

function* setPasswordPatch(action) {
    const { identityId, password } = action.payload;
    const url = `${config.api.authUrl}${config.api.paths.setPassword}`;
    try {
        const res = yield call(http.authorized.patch, url, {
            identityId,
            password,
        });
        yield put(actionCreators.setPasswordPatchSuccess());
    } catch (error) {
        yield put(actionCreators.setPasswordPatchFailure(error));
    }
}

function* logout() {
    try {
        localStorage.setItem("persist:root", "{}");
    } catch (error) {
        console.log(error);
        yield put(actionCreators.getFailure(error.toString()));
    }
}

export default (function* () {
    yield all([
        takeLatest(actionTypes.AUTH_GET_REQUEST, get),
        takeLatest(actionTypes.AUTH_LOGOUT, logout),
        takeLatest(
            actionTypes.AUTH_SET_PASSWORD_PATCH_REQUEST,
            setPasswordPatch
        ),
        takeLatest(
            actionTypes.AUTH_REGISTRATION_POST_REQUEST,
            postRegistrationRequest
        ),
        takeLatest(
            actionTypes.AUTH_REGISTRATION_GET_REQUEST,
            getRegistrationRequest
        ),
    ]);
})();
