export const actionTypes = {
  AUDITS_STARTED_CLEAR: 'AUDITS_STARTED_CLEAR',
  AUDITS_STARTED_UPDATE: 'AUDITS_STARTED_UPDATE',
};

export const actionCreators = {
  clear: () => ({
    type: actionTypes.AUDITS_STARTED_CLEAR,
  }),
  update: (auditsStarted: Array<number>) => ({
    type: actionTypes.AUDITS_STARTED_UPDATE,
    payload: { auditsStarted },
  }),
};

export default {
  create: actionCreators,
  type: actionTypes,
};
