import config from "../config";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
export default () => {
    const { domain, privacyUrl, supportUrl } = config.api;
    const { version } = config;
    const { auth } = useSelector((state) => state);
    const { account } = auth;
    const name = `${account.firstName} ${account.surname}`;
    const { t } = useTranslation();
    return [
        {
            label: t("backgroundSync"),
            icon: false,
            path: `${domain}${supportUrl}`,
            secondaryText: null,
            actions: true,
        },
        {
            label: t("support"),
            icon: true,
            path: `${domain}${supportUrl}`,
            secondaryText: null,
            actions: false,
        },
        {
            label: t("privacyPolicy"),
            icon: true,
            path: `${domain}${privacyUrl}?name=${name}&email=${account.email}`,
            secondaryText: null,
            actions: false,
        },
        {
            label: t("version"),
            icon: false,
            path: null,
            secondaryText: version,
            actions: false,
        },
    ];
};
