export const actionTypes = {
    AUDIT_COMPLETION_INIT: "AUDIT_COMPLETION_INIT",
    AUDIT_COMPLETION_SET: "AUDIT_COMPLETION_SET",
};

export const actionCreators = {
    init: (auditId: number) => ({
        type: actionTypes.AUDIT_COMPLETION_INIT,
        payload: { auditId },
    }),
    set: (auditCompletion: any) => ({
        type: actionTypes.AUDIT_COMPLETION_SET,
        payload: { auditCompletion },
    }),
};

export default {
    create: actionCreators,
    type: actionTypes,
};
