import { all, call, put, select, takeLatest } from "redux-saga/effects";
import moment from "moment";
import helpers from "../../common/lib/helpers";
import { db } from "../../common/lib";
import config from "../../config";
import actions from "../actions";
import { actionCreators, actionTypes } from "./actions";
import { get } from "idb-keyval";

function* setInitialValues(action) {
    const { auditId } = action.payload;
    const { auditCompletion, auditLocations, audits, auditsStarted } =
        yield select((state) => state);

    let audit = helpers.list.getItemForId(audits, auditId);

    if (Object.keys(audit).length > 0) {
        let altAssets = (yield call(get, db.types.altAsset)) || [];
        if (altAssets && altAssets.length > 0)
            altAssets = altAssets.filter(
                (item) => item.audit.id === parseInt(auditId)
            );

        let locationsForAudit = yield call(get, db.types.auditLocation);
        if (locationsForAudit) {
            locationsForAudit = locationsForAudit.filter((item) => {
                item.audit.id === audit.id;
            });
        }

        /** Required Asset for auditAsset */
        let requiredAssets = yield call(get, db.types.auditAsset);
        requiredAssets = requiredAssets.filter(
            (item) => item.auditLocation.audit.id === audit.id
        );

        const assignedAssets = requiredAssets.filter((i) => i.isAssigned);
        let assignTimes = 0;
        let lastAssignTime = 0;
        let approxTimeLeft = 0;

        yield put(
            actionCreators.set({
                alt: altAssets.length,
                approxTimeLeft,
                assigned: assignedAssets.length,
                assignTimes,
                isComplete: requiredAssets.length - assignedAssets.length === 0,
                // isStarted: Boolean(auditsStarted[audits.item.id]), TODO:// Redux persistent library should be used
                isStarted: Boolean(audit.id),
                lastAssignTime: moment().toISOString(),
                required: requiredAssets.length,
            })
        );
    }
}

export default (function* () {
    yield all([
        takeLatest(actionTypes.AUDIT_COMPLETION_INIT, setInitialValues),
        takeLatest(
            actions.altAssets.type.ALT_ASSETS_WRITE_SUCCESS,
            setInitialValues
        ),
        takeLatest(
            actions.assetAuditors.type.ASSET_AUDITORS_WRITE_SUCCESS,
            setInitialValues
        ),
        takeLatest(
            actions.auditAssets.type.AUDIT_ASSETS_WRITE_SUCCESS,
            setInitialValues
        ),
    ]);
})();

