import { ERROR_ACTION, WAIT_FOR_ACTION } from 'redux-wait-for-action';

export const actionTypes = {
  AUDIT_ASSIGNMENTS_SELECT_FAILURE: 'AUDIT_ASSIGNMENTS_SELECT_FAILURE',
  AUDIT_ASSIGNMENTS_SELECT_REQUEST: 'AUDIT_ASSIGNMENTS_SELECT_REQUEST',
  AUDIT_ASSIGNMENTS_SELECT_SUCCESS: 'AUDIT_ASSIGNMENTS_SELECT_SUCCESS',
};

export const actionCreators = {
  selectRequest: () => ({
    type: actionTypes.AUDIT_ASSIGNMENTS_SELECT_REQUEST,
    [WAIT_FOR_ACTION]: actionTypes.AUDIT_ASSIGNMENTS_SELECT_SUCCESS,
    [ERROR_ACTION]: actionTypes.AUDIT_ASSIGNMENTS_SELECT_FAILURE,
  }),
  selectSuccess: (auditAssignments: Array<any>) => ({
    type: actionTypes.AUDIT_ASSIGNMENTS_SELECT_SUCCESS,
    payload: { auditAssignments },
  }),
  selectFailure: (error: Error) => ({
    type: actionTypes.AUDIT_ASSIGNMENTS_SELECT_FAILURE,
    error,
  }),
};

export default {
  create: actionCreators,
  type: actionTypes,
};
