import React, { useState } from "react";
import { useSelector } from "react-redux";
import imageCompression from "browser-image-compression";
import { v4 as uuid } from "uuid";
import { useImageTypeOverlay } from "../../hooks";
import AppModal from "../AppModal";
import TakePhoto from "./components/TakePhoto";
import SavePhoto from "./components/SavePhoto";

import { useImageTypes } from "../../hooks";
import useSelectImageType from "./useSelectImageType";

type Props = {
    assetTypeIds: Array<number>,
    exclusionReasonTypeId: number,
    isVisible: boolean,
    lastPhoto: any,
    onClose: Function,
    onSuccess: Function,
};

const defaultPhoto = {
    uri: "",
    width: 0,
    height: 0,
};

/**
 * Camera Component
 */
export default ({
    assetTypeIds = [],
    exclusionReasonTypeId,
    isVisible,
    lastPhoto,
    onClose,
    onSuccess,
}: Props) => {
    const { geoLocation } = useSelector((state) => state);
    const [photo, setPhoto] = useState(defaultPhoto);

    const imageTypes = useImageTypes(assetTypeIds, exclusionReasonTypeId);
    const selectedImageType = useSelectImageType(
        exclusionReasonTypeId,
        lastPhoto,
        imageTypes
    );
    const overlay = useImageTypeOverlay((selectedImageType || {}).id);

    const onSavePhotoSuccess = async () => {
        const options = {
            maxSizeMB: 1,
            maxWidthOrHeight: 1024,
            initialQuality: 0.85,
            fileType: "image/jpeg",
            useWebWorker: true,
        };

        // compress image
        const imageFile = await imageCompression.getFilefromDataUrl(photo.uri);
        const compressedFile = await imageCompression(imageFile, options);
        const imageUrl = await imageCompression.getDataUrlFromFile(
            compressedFile
        );

        // left for debugging
        /*
            const sizeBefore = imageFile.size / 1024 / 1024;
            const sizeAfter = compressedFile.size / 1024 / 1024;
            alert(`size before/after:${sizeBefore}/${sizeAfter}`);
        */

        onSuccess &&
            onSuccess({
                id: uuid(),
                imageType: selectedImageType.id,
                createdDate: new Date().toISOString(),
                image: imageUrl,
                latitude: geoLocation.coords.latitude,
                longitude: geoLocation.coords.longitude,
                uploaded: false,
            });

        setPhoto(defaultPhoto);
    };

    const onPhotoClear = () => setPhoto(defaultPhoto);

    let title = "";
    if (selectedImageType) {
        title = selectedImageType.name;
    }

    return (
        <AppModal isVisible={isVisible} onClose={onClose} title={title}>
            {photo.uri ? (
                <SavePhoto
                    onRetakePhoto={onPhotoClear}
                    onSuccess={onSavePhotoSuccess}
                    photo={photo}
                />
            ) : (
                <TakePhoto onSuccess={setPhoto} overlay={overlay} />
            )}
        </AppModal>
    );
};
