import { actionTypes } from "./actions";
import moment from "moment";

const initialState = {
    account: {},
    accessToken: "",
    expiryDate: moment(0).toISOString(),
    idToken: "",
    refreshToken: "",
    tokenType: "",
    displayName: "",
    username: "",
    regStatus: "",
};

export default function (state = initialState, action) {
    switch (action.type) {
        case actionTypes.AUTH_GET_REQUEST: {
            return { ...initialState };
        }
        case actionTypes.AUTH_GET_SUCCESS: {
            return { ...action.payload };
        }
        case actionTypes.AUTH_LOAD_SESSION: {
            return { ...action.payload };
        }
        case actionTypes.AUTH_LOGOUT: {
            const { username } = state;
            return { ...initialState, username };
        }
        case actionTypes.AUTH_REGISTRATION_POST_SUCCESS: {
            const { data } = action.payload;
            return { ...initialState, regStatus: data };
        }
        case actionTypes.AUTH_REGISTRATION_GET_SUCCESS: {
            const { data } = action.payload;
            return { ...initialState, displayName: data.name };
        }
        default: {
            return state;
        }
    }
}
