import React, { useEffect, useState } from "react";
import {
    Route,
    Routes,
    Navigate,
    useLocation,
    useNavigate,
    Outlet,
} from "react-router-dom";
import config from "./config";

import Auth from "./Auth";
import Login from "./Login";
import Logout from "./Logout";

import Audits from "./Audits";
import Audit from "./Audit";
import AltAsset from "./AltAsset";
import AuditSubmission from "./Audit/AuditSubmission";
import AuditDetails from "./Audit/AuditDetails";
import AuditAsset from "./AuditAsset";
import AuditAssets from "./AuditAssets";
import AssetDetails from "./AuditAsset/AssetDetails";
import AuditAssetExclude from "./AuditAssetExclude";
import AssetSearch from "./AuditAssets/Search";
import Settings from "./Settings";

const paths = {
    auth: `/${config.paths.auth}`,
    login: `/${config.paths.login}`,
    logout: `/${config.paths.logout}`,
    audits: `${config.paths.audits}`,
    settings: `${config.paths.settings}`,
    auditAssets: `${config.paths.auditAssets}`,
    home: `/${config.paths.auth}/${config.paths.audits}`,
};

/**
 * App Routes Component
 */
export default () => {
    const [redirectRequired, setRedirectRequired] = useState(false);
    const navigate = useNavigate();
    const location = useLocation();

    const setRedict = () => setRedirectRequired(true);

    // TODO: is there a better way to redirect using react-router-dom v6
    useEffect(() => {
        if (redirectRequired) {
            navigate(`${paths.home}/list`, { replace: true });
            // // do manual refresh, otherwise it brings back to current pathname
            navigate(0);
        }
    });

    useEffect(() => {
        window.addEventListener("beforeunload", setRedict);
        return () => {
            window.removeEventListener("beforeunload", setRedict);
        };
    }, []);

    const CustomWrapper = () => {
        return !redirectRequired ? (
            <Auth />
        ) : (
            <Navigate to={`${paths.home}/list`} replace state={{ location }} />
        );
    };

    return (
        <Routes>
            <Route path={paths.auth} element={<CustomWrapper />}>
                <Route index element={<Audits />} />
                <Route path={paths.audits} element={<Outlet />}>
                    <Route index element={<Audits />} />
                    <Route path={"list"} element={<Audits />} />
                    {/* /auth/audits/1 */}
                    <Route path={":auditId"} element={<Audit />}>
                        <Route path={"details"} element={<AuditDetails />} />
                        {/* /auth/audits/1/assets */}
                        <Route path={"assets"} element={<Outlet />}>
                            <Route path={"list"} element={<AuditAssets />} />
                            <Route
                                path={"exclude-remaining"}
                                element={<AuditAssetExclude />}
                            />
                            {/* /auth/audits/1/assets/1 */}
                            <Route path={":assetId"} element={<AuditAsset />}>
                                <Route
                                    path={"details"}
                                    element={<AssetDetails />}
                                />
                                <Route
                                    path={"exclude"}
                                    element={<AuditAssetExclude />}
                                />
                            </Route>
                        </Route>
                        <Route path={"new-asset"} element={<AltAsset />} />
                        <Route path={"search"} element={<AssetSearch />} />
                        <Route path={"submit"} element={<AuditSubmission />} />
                    </Route>
                </Route>
                <Route path={paths.auditAssets} element={<AuditAssets />} />
                <Route path={paths.settings} element={<Settings />} />
            </Route>
            <Route path={"/"} element={<Login />} />
            <Route path={paths.login} element={<Login />} />
            <Route path={paths.logout} element={<Logout />} />
        </Routes>
    );
};

