/**  Coomon for image data construction for the altAssets and assetAuditors */
import db from "../../common/lib/database";
import { get, update } from "idb-keyval";

/** Image construction for auditAsset and AltAsset */
const mapAuditImages = (auditImages: Array) =>
    auditImages.map(
        ({ createdDate, imageType, latitude, longitude, image, uploaded }) => ({
            createdDate,
            imageType,
            latitude,
            longitude,
            path: image,
            uploaded,
        })
    );

/**  Object construction for the altAssets */
const mapAltAsset = async (altAsset: any) => {
    const auditImages = altAsset.auditImages
        ? mapAuditImages(altAsset.auditImages)
        : [];
    return {
        altAsset: {
            createdBy: altAsset.createdBy,
            createdDate: altAsset.createdDate,
            identifier1: altAsset.identifier1,
            identifier2: altAsset.identifier2,
            identifier3: altAsset.identifier3,
            identifier4: altAsset.identifier4,
            make: altAsset.make,
            type: altAsset.type,
        },
        auditImages,
        notes: altAsset.notes ? altAsset.notes : [],
    };
};

/** Object construction for the  assetAuditor  */
const mapAssetAuditor = async (assetAuditor: any) => {
    const auditImages = assetAuditor.auditImages
        ? mapAuditImages(assetAuditor.auditImages)
        : [];

    return {
        assetAuditor: {
            assignedDate: assetAuditor.assignedDate,
            auditAsset: assetAuditor.auditAsset.id,
            auditor: assetAuditor.auditAssignment.auditor,
            exclusionReason: assetAuditor.exclusionReason,
        },
        assetAttributes: assetAuditor.auditAsset.assetAttributes,
        auditImages,
        auditLocation: assetAuditor.auditAssignment.auditLocation.id,
        notes: assetAuditor.notes ? assetAuditor.notes : [],
    };
};

/** Clearing data of Altassets, AsssetAuditor, AuditAssets and AuditAssignments */
const cleanAllData = async (audit) => {
    const altAssetsDB = (await get(db.types.altAsset)) || [];
    const altAssets = db.queries.forAudit(audit.id, altAssetsDB);
    const altAssetsIds = altAssets.map((item) => item.id);

    // Update Alt Assets by filtering out alt  assets for submitted audit and updating db
    await update(db.types.altAsset, (updatedAltAssetsDB = []) => {
        return updatedAltAssetsDB.filter(
            (item) => !altAssetsIds.includes(item.id)
        );
    });

    //Clear Asset Auditor this can be retrived through auditAssignment DB and map the ids with assetAuditors
    const auditAssignmentDB = (await get(db.types.auditAssignment)) || [];
    const auditAssignments = db.queries.forAuditLocationAudit(
        audit.id,
        auditAssignmentDB
    );
    const assignmentIds = auditAssignments.map((item) => item.id);

    const assetAuditorsDB = (await get(db.types.assetAuditor)) || [];
    const assetAuditors = db.queries.forAuditAssignments(
        assignmentIds,
        assetAuditorsDB
    );
    const assetAuditorsId = assetAuditors.map((item) => `${item.id}`);

    // Update assetAuditors by filtering out assets for submitted audit and updating db
    await update(db.types.assetAuditor, (updatedAssetAuditorsDB = []) => {
        return updatedAssetAuditorsDB.filter(
            (item) => !assetAuditorsId.includes(item.id)
        );
    });

    //Audit Assignment
    const auditLocationIds = auditAssignments.map(
        (item) => item.auditLocation.id
    );

    // Update Audit Assignments by by filtering out assignments for submitted audit and updating db
    await update(db.types.auditAssignment, (updatedAssignmentsDB = []) => {
        return updatedAssignmentsDB.filter(
            (item) => !assignmentIds.includes(item.id)
        );
    });

    // After updating the auditAssignment check for the remaining ids and its length
    const updatedAuditAssignment = (await get(db.types.auditAssignment)) || [];
    const updatedAssignmentIds = updatedAuditAssignment.map((item) => item.id);
    //Clear Audit Assets if there are no more assignments remove the auditAsset

    if (updatedAssignmentIds.length === 0) {
        // Update Audit Asset
        await update(db.types.auditAsset, (updatedAuditAssetDB = []) => {
            return updatedAuditAssetDB.filter(
                (item) => !auditLocationIds.includes(item.auditLocation.id)
            );
        });
    }
};

export default {
    cleanAllData,
    mapAssetAuditor,
    mapAltAsset,
    mapAuditImages,
};
