import React, { useState } from "react";
import * as Sentry from "@sentry/react";
import { Box, Grid, Typography, CircularProgress } from "@mui/material/";
import LoadingButton from "@mui/lab/LoadingButton";
import makeStyles from "@mui/styles/makeStyles";
import SendIcon from "@mui/icons-material/Send";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { useTranslation } from "react-i18next";
import actions from "../Store/actions";
import styles from "./styles";
import { ActionBar } from "../common/components";
import { useToaster, useAuditProgressDetails } from "../common/hooks";

const AuditSubmission = () => {
    const classes = makeStyles(styles)();
    const { auditId } = useParams();
    const { t } = useTranslation();
    let navigate = useNavigate();
    const [loading, setLoading] = useState(false);
    const dispatch = useDispatch();
    const toast = useToaster();
    const { auditUpload } = useSelector((state) => state);
    const { isComplete } = useAuditProgressDetails("db", auditId);

    const { total, uploaded } = auditUpload;

    const onSubmit = async () => {
        try {
            setLoading(true);
            await dispatch(actions.auditUpload.create.postRequest());
            navigate(`/auth/audits/list`);
        } catch (error) {
            console.log(error);
            toast.error(t("errors.auditUploadPost"));
            Sentry.captureException(error);
            setLoading(false);
        }
    };
    const percent = Math.round(100 * (uploaded / total));

    return (
        <>
            <Box className={classes.auditSubmissionContainer}>
                <Typography component="p" align="center">
                    {t("messages.auditSubmit")}
                </Typography>
                <ActionBar>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <LoadingButton
                                fullWidth
                                disabled={loading}
                                loading={loading}
                                loadingPosition="end"
                                endIcon={<SendIcon />}
                                onClick={onSubmit}
                                variant={isComplete ? "contained" : "outlined"}
                            >
                                {t(
                                    isComplete
                                        ? "submitAudit"
                                        : "submitPartialAudit"
                                )}
                            </LoadingButton>
                        </Grid>
                    </Grid>
                </ActionBar>
            </Box>
            {loading && (
                <Box className={classes.uploadMessageContainer}>
                    {Boolean(total) && (
                        <>
                            <Typography component="p" align="center">
                                {`${t("uploading")}: ${
                                    isNaN(percent) ? 0 : percent
                                }%`}
                                <CircularProgress color="primary" />
                            </Typography>

                            <Typography component="p" align="center">
                                {t("messages.uploading")}
                            </Typography>
                        </>
                    )}
                </Box>
            )}
        </>
    );
};

export default AuditSubmission;
