import React, { useState } from "react";
import { v4 as uuid } from "uuid";
import * as Sentry from "@sentry/react";
import { useNavigate, useParams, useLocation } from "react-router-dom";
import { useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";
import actions from "../Store/actions";
import useAuditAsset from "../common/hooks/useAuditAsset";
import Attributes from "./components/Attributes";
import Core from "./components/Core";
import { Camera, PhotoList, ActionBar } from "../common/components";
import makeStyles from "@mui/styles/makeStyles";
import styles from "./styles";

import {
    Box,
    Button,
    Typography,
    Grid,
    Alert,
    AlertTitle,
    TextField,
} from "@mui/material/";
import CheckIcon from "@mui/icons-material/Check";
import HighlightOffIcon from "@mui/icons-material/HighlightOff";
import AddAPhotoIcon from "@mui/icons-material/AddAPhoto";
import {
    useAuditAssignment,
    useAuditBranch,
    useToaster,
} from "../common/hooks";

const AssetDetails = () => {
    let navigate = useNavigate();
    const location = useLocation();
    const { t } = useTranslation();
    const classes = makeStyles(styles)();
    const { auditId, assetId } = useParams();

    const dispatch = useDispatch();
    const toast = useToaster();
    const asset = useAuditAsset(assetId);
    const auditAssignment = useAuditAssignment(
        asset ? asset.auditLocation.id : null
    );

    const branch = useAuditBranch(auditAssignment);

    const [loading, setLoading] = useState(false);
    const [modalVisible, setModalVisible] = useState(null);
    const [note, setNote] = useState({
        content: "",
        createdDate: null,
        id: uuid(),
    });
    const [photos, setPhotos] = useState([]);

    const onCheckAssetPress = async () => {
        setLoading(true);
        try {
            await dispatch(
                actions.assetAuditors.create.writeRequest([
                    {
                        id: uuid(),
                        assignedDate: new Date().toISOString(),
                        auditAsset: {
                            id: parseInt(assetId),
                            isAssigned: true,
                        },
                        exclusionReason: null,
                        auditAssignment,
                        auditImages: photos,
                        notes: note.content
                            ? [
                                  {
                                      ...note,
                                      createdDate: new Date().toISOString(),
                                  },
                              ]
                            : [],
                    },
                ])
            );
            setLoading(false);
            navigate(`/auth/audits/${auditId}/assets/list`);
        } catch (error) {
            console.log(error);
            Sentry.captureException(error);
            toast.error(t("errors.checkAsset"));
            setLoading(false);
        }
    };

    const onExcludePress = () =>
        navigate(`/auth/audits/${auditId}/assets/${assetId}/exclude`);

    const onModalHide = () => setModalVisible(null);
    const onModalShowCamera = () => setModalVisible("CAMERA");

    const onNoteChangeText = (e: any) => {
        const { value } = e.target;
        setNote({ ...note, content: value });
    };

    const onPhotoChange = (photo: any) => {
        const next = photos.map((item) =>
            item.id === photo.id ? photo : item
        );
        setPhotos(next);
    };

    const onPhotoDelete = (photo: any) => {
        setPhotos(photos.filter(({ id }) => id !== photo.id));
    };

    const onPhotoSuccess = (photo: any) => {
        setPhotos([photo, ...photos]);
        onModalHide();
    };

    const hasPhotos = Boolean(photos.length);
    const isLinked = location.state && location.state.linked;

    // stopper for processed asset
    if (asset && asset.isAssigned)
        return <Box sx={{ p: 2 }}>{t("messages.assetAlreadyChecked")}</Box>;

    return (
        <>
            {isLinked && (
                <Alert severity="info">
                    <AlertTitle>Info</AlertTitle>
                    {t("messages.assetLinked", { branch })}
                </Alert>
            )}
            <Core />
            <Attributes />

            {hasPhotos ? (
                <>
                    <Grid
                        container
                        spacing={2}
                        className={
                            asset.assetAttributes > 0
                                ? classes.padding
                                : classes.moveToBottom
                        }
                    >
                        <Grid item xs={12}>
                            <PhotoList
                                assetTypeId={asset.assetCore.type}
                                onChange={onPhotoChange}
                                onDelete={onPhotoDelete}
                                photos={photos}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                id="outlined-textarea"
                                placeholder="Add your note"
                                onChange={onNoteChangeText}
                                value={note.content}
                                multiline
                                fullWidth
                                rows={4}
                            />
                        </Grid>
                    </Grid>

                    <ActionBar>
                        <Grid container spacing={2}>
                            <Grid item xs={6}>
                                <Button
                                    variant="outlined"
                                    color="primary"
                                    disabled={loading}
                                    fullWidth
                                    onClick={onModalShowCamera}
                                    endIcon={<AddAPhotoIcon />}
                                    style={{ marginBottom: ".5rem" }}
                                >
                                    {t("addPhoto")}
                                </Button>
                            </Grid>
                            <Grid item xs={6}>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    disabled={loading}
                                    fullWidth
                                    onClick={onCheckAssetPress}
                                    endIcon={<CheckIcon />}
                                >
                                    {t("checkAsset")}
                                </Button>
                            </Grid>
                        </Grid>
                    </ActionBar>
                </>
            ) : (
                <ActionBar>
                    <Grid container className={classes.yesNoActions}>
                        <Grid item xs={12}>
                            <Typography
                                variant="body2"
                                component="div"
                                style={{ paddingBottom: ".5rem" }}
                            >
                                {t("assetIsHere")}
                            </Typography>
                        </Grid>
                        <Grid container spacing={2}>
                            <Grid item xs={6}>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    disabled={loading}
                                    fullWidth
                                    onClick={onModalShowCamera}
                                    endIcon={<CheckIcon />}
                                >
                                    {t("yes")}
                                </Button>
                            </Grid>
                            <Grid item xs={6}>
                                <Button
                                    variant="outlined"
                                    color="primary"
                                    disabled={loading}
                                    fullWidth
                                    onClick={onExcludePress}
                                    endIcon={<HighlightOffIcon />}
                                >
                                    {t("no")}
                                </Button>
                            </Grid>
                        </Grid>
                    </Grid>
                </ActionBar>
            )}

            {asset && (
                <Camera
                    assetTypeIds={[asset.assetCore.type]}
                    isVisible={modalVisible === "CAMERA"}
                    lastPhoto={photos[0]}
                    onClose={onModalHide}
                    onSuccess={onPhotoSuccess}
                />
            )}
        </>
    );
};

export default AssetDetails;
