import { useMemo } from "react";
import { useSelector } from "react-redux";

export default () => {
    const { auditAssets } = useSelector((state) => state);

    const dataUnchecked = useMemo(() => {
        return auditAssets.data
            .filter((item) => item.isAssigned === false)
            .sort((a, b) => a.deferredTimestamp - b.deferredTimestamp);
    }, [auditAssets]);
    return dataUnchecked;
};
