import list from "../../common/lib/helpers/list";
import getInitialState from "../get-initial-state";
import { actionTypes } from "./actions";

const initialState = getInitialState.list();

export default function (state = initialState, action) {
    switch (action.type) {
        case actionTypes.AUDIT_ASSET_ITEM_CLEAR: {
            return { ...state, item: null };
        }
        case actionTypes.AUDIT_ASSET_ITEM_SET: {
            const { auditAsset } = action.payload;
            return { ...state, item: auditAsset };
        }
        case actionTypes.AUDIT_ASSET_ITEM_UPDATE: {
            const { auditAsset } = action.payload;
            const { item } = state;
            return { ...state, item: { ...item, ...auditAsset } };
        }
        case actionTypes.AUDIT_ASSETS_CLEAR: {
            const { item } = state;
            return { ...initialState, item };
        }
        case actionTypes.AUDIT_ASSETS_DATA_UPD: {
            const { assetIds } = action.payload;
            const { data } = state;
            const updateData = [...data];

            updateData.map((obj) => {
                if (assetIds.includes(obj.id)) obj.isAssigned = true;
                return obj;
            });
            return { ...state, data: updateData };
        }
        case actionTypes.AUDIT_ASSETS_SELECT_SUCCESS: {
            const { auditAssets } = action.payload;
            return list.getReducer(state, auditAssets);
        }
        case actionTypes.AUDIT_ASSETS_WRITE_SUCCESS: {
            const { auditAssets } = action.payload;
            return list.mergeReducer(state, auditAssets);
        }
        default: {
            return state;
        }
    }
}
