import list from "../../common/lib/helpers/list";
import getInitialState from "../get-initial-state";
import { actionTypes } from "./actions";

const initialState = getInitialState.list();

export default function (state = { ...initialState }, action) {
    switch (action.type) {
        case actionTypes.AUDIT_ITEM_SET: {
            const { audit } = action.payload;
            return { ...state, item: audit };
        }
        case actionTypes.AUDITS_SELECT_SUCCESS: {
            const { audits } = action.payload;
            return list.getReducer(state, audits);
        }
        case actionTypes.AUDITS_WRITE_SUCCESS: {
            const { audits } = action.payload;
            return list.mergeReducer(state, audits);
        }
        default: {
            return state;
        }
    }
}
