const PREVIEW_SIZE = window.outerWidth;

const getListOfVideoInputs = async () => {
    // Get the details of audio and video output of the device
    const enumerateDevices = await navigator.mediaDevices.enumerateDevices();

    //Filter video outputs (for devices with multiple cameras)
    return enumerateDevices.filter((device) => device.kind === "videoinput");
};

const initializeMedia = async (player, setIsCameraLoading) => {
    if (!("mediaDevices" in navigator)) {
        navigator.mediaDevices = {};
    }

    if (!("getUserMedia" in navigator.mediaDevices)) {
        navigator.mediaDevices.getUserMedia = function (constraints) {
            var getUserMedia =
                navigator.webkitGetUserMedia || navigator.mozGetUserMedia;

            if (!getUserMedia) {
                return Promise.reject(
                    new Error("getUserMedia Not Implemented")
                );
            }

            return new Promise((resolve, reject) => {
                getUserMedia.call(navigator, constraints, resolve, reject);
            });
        };
    }

    //Get the details of video inputs of the device
    const videoInputs = await getListOfVideoInputs();
    const isLocalEnv = process.env.HOST === "local";

    //The device has a camera
    if (videoInputs.length) {
        const camera = videoInputs[videoInputs.length - 1];

        navigator.mediaDevices
            .getUserMedia({
                video: {
                    deviceId: camera.deviceId,
                    minAspectRatio: 1.333,
                    minFrameRate: 30,
                    width: isLocalEnv ? 720 : 1080,
                    height: isLocalEnv ? 1280 : 1920,
                    facingMode: isLocalEnv ? "user" : "environment",
                },
                audio: false,
            })
            .then((stream) => {
                player.current.srcObject = stream;
                setIsCameraLoading(false);
            })
            .catch((error) => {
                console.error(error);
            });
    } else {
        alert("The device does not have a camera");
    }
};

export { initializeMedia };
