import { all, call, put, takeLatest } from "redux-saga/effects";
import { db } from "../../common/lib";
import { actionCreators, actionTypes } from "./actions";
import { get, update } from "idb-keyval";

function* select(action) {
    // const { auditId } = action.payload;
    try {
        let altAssets = (yield call(get, db.types.altAsset)) || [];

        yield put(actionCreators.selectSuccess(altAssets));
    } catch (error) {
        yield put(actionCreators.selectFailure(error.toString()));
    }
}

function* write(action) {
    const { altAssets } = action.payload;
    try {
        yield call(update, db.types.altAsset, (altAssetsDB = []) => [
            ...altAssetsDB,
            altAssets[0],
        ]);

        yield put(actionCreators.writeSuccess(altAssets[0]));
    } catch (error) {
        yield put(actionCreators.writeFailure(error.toString()));
    }
}

export { select, write };

export default (function* () {
    yield all([
        takeLatest(actionTypes.ALT_ASSETS_SELECT_REQUEST, select),
        takeLatest(actionTypes.ALT_ASSETS_WRITE_REQUEST, write),
    ]);
})();
