import { ERROR_ACTION, WAIT_FOR_ACTION } from 'redux-wait-for-action';

export const actionTypes = {
  AUDIT_UPLOAD_POST_FAILURE: 'AUDIT_UPLOAD_POST_FAILURE',
  AUDIT_UPLOAD_POST_REQUEST: 'AUDIT_UPLOAD_POST_REQUEST',
  AUDIT_UPLOAD_POST_SUCCESS: 'AUDIT_UPLOAD_POST_SUCCESS',

  AUDIT_UPLOAD_STATUS_INIT: 'AUDIT_UPLOAD_STATUS_INIT',
  AUDIT_UPLOAD_STATUS_SET: 'AUDIT_UPLOAD_STATUS_SET',
  AUDIT_UPLOAD_STATUS_UPLOADED: 'AUDIT_UPLOAD_STATUS_UPLOADED',
};

export const actionCreators = {
  postRequest: () => ({
    type: actionTypes.AUDIT_UPLOAD_POST_REQUEST,
    [WAIT_FOR_ACTION]: actionTypes.AUDIT_UPLOAD_POST_SUCCESS,
    [ERROR_ACTION]: actionTypes.AUDIT_UPLOAD_POST_FAILURE,
  }),
  postSuccess: () => ({
    type: actionTypes.AUDIT_UPLOAD_POST_SUCCESS,
  }),
  postFailure: (error) => ({
    type: actionTypes.AUDIT_UPLOAD_POST_FAILURE,
    error,
  }),

  statusInit: () => ({
    type: actionTypes.AUDIT_UPLOAD_STATUS_INIT,
  }),
  statusSet: (status: any) => ({
    type: actionTypes.AUDIT_UPLOAD_STATUS_SET,
    payload: { status },
  }),
  statusUploaded: (uploaded: number) => ({
    type: actionTypes.AUDIT_UPLOAD_STATUS_UPLOADED,
    payload: { uploaded },
  }),
};

export default {
  create: actionCreators,
  type: actionTypes,
};
