import { all, call, put, takeLatest } from "redux-saga/effects";
import { uniqBy } from "lodash";
import i18next from "../../common/i18n";
import { db, enums, http } from "../../common/lib";
import { actionCreators, actionTypes } from "./actions";
import { actionCreators as toastActions } from "../toast/actions";
import { update } from "idb-keyval";
import config from "../../config";

const format = (data: Array<any>) => {
    const altAssets = [];
    const auditAssets = [];
    const auditAssignments = [];
    const audits = [];
    for (let d of data) {
        const { auditLocation } = d;
        const { audit } = auditLocation;
        altAssets.push(...d.altAssets.map((item) => ({ ...item, audit })));
        auditAssets.push(
            ...d.auditAssets.map((item) => ({
                ...item,
                auditLocation,
                deferredTimestamp: 0,
            }))
        );
        auditAssignments.push({ ...d.auditAssignment, auditLocation });
        audits.push(auditLocation.audit);
    }
    return [altAssets, auditAssets, auditAssignments, uniqBy(audits, "id")];
};

function* get() {
    try {
        const { data } = yield call(
            http.authorized.get,
            config.api.paths.auditDownload
        );

        const [altAssets, auditAssets, auditAssignments, audits] = format(data);

        if (auditAssignments.length > 0) {
            yield call(
                http.authorized.post,
                config.api.paths.auditAssignmentBulkUpdate,
                auditAssignments.map((item) => ({
                    id: item.id,
                    status: enums.auditAssignment.pendingSubmission,
                }))
            );
        }

        if (altAssets.length > 0)
            yield call(update, db.types.altAsset, (altAssetsDB = []) => {
                return [...altAssets, ...altAssets];
            });

        if (auditAssets.length > 0)
            yield call(update, db.types.auditAsset, (auditAssetsDB = []) => [
                ...auditAssetsDB,
                ...auditAssets,
            ]);

        if (auditAssignments.length > 0)
            yield call(
                update,
                db.types.auditAssignment,
                (auditAssignmentsDB = []) => [
                    ...auditAssignmentsDB,
                    ...auditAssignments,
                ]
            );

        yield put(actionCreators.getSuccess(audits));
    } catch (error) {
        yield all([
            put(actionCreators.getFailure(error.toString())),
            put(toastActions.error(i18next.t("errors.auditDownloadGet"))),
        ]);
    }
}

export default (function* () {
    yield all([takeLatest(actionTypes.AUDIT_DOWNLOAD_GET_REQUEST, get)]);
})();
