import getInitialState from "../get-initial-state";
import { actionTypes } from "./actions";
import helpers from "../../common/lib/helpers";

const initialState = getInitialState.list();

export default function (state = { ...initialState }, action) {
    switch (action.type) {
        case actionTypes.ALT_ASSET_ITEM_CLEAR: {
            return { ...state, item: getInitialState.altAsset() };
        }
        case actionTypes.ALT_ASSET_ITEM_SET: {
            const { altAsset } = action.payload;
            return { ...state, item: altAsset };
        }
        case actionTypes.ALT_ASSET_ITEM_UPDATE: {
            const { altAsset } = action.payload;
            const { item } = state;
            return { ...state, item: { ...item, ...altAsset } };
        }
        case actionTypes.ALT_ASSETS_CLEAR: {
            return initialState;
        }
        case actionTypes.ALT_ASSETS_SELECT_SUCCESS: {
            const { altAssets } = action.payload;
            return helpers.list.getReducer(state, altAssets);
        }
        case actionTypes.ALT_ASSETS_WRITE_SUCCESS: {
            const { altAssets } = action.payload;
            return helpers.list.mergeReducer(state, altAssets);
        }
        default: {
            return state;
        }
    }
}
