import React, { useMemo } from "react";
// TODO: replace lodash with custom functions as lodash is quite heavy
import { chunk } from "lodash";
import { useParams } from "react-router-dom";
import { useAssetTypeAttributes } from "../../common/hooks";
import useAuditAsset from "../../common/hooks/useAuditAsset";

import { Grid, Box, Typography } from "@mui/material/";
import makeStyles from "@mui/styles/makeStyles";
import styles from "./styles";

/**
 * Audit Asset Attributes Component
 */
export default () => {
    const { assetId } = useParams();
    const asset = useAuditAsset(assetId);
    const classes = makeStyles(styles)();

    const { assetAttributes, assetCore } = asset;
    const attributes = useAssetTypeAttributes(assetCore.type);

    const rows = useMemo(() => chunk(assetAttributes, 2), [assetAttributes]);

    return (
        <>
            {rows.map(([first, second], index) => (
                <Grid container key={index} className={classes.tbPadding}>
                    <Grid item xs={6}>
                        <Typography variant="subtitle1" component="div">
                            <Box component="span" fontWeight="fontWeightMedium">
                                {attributes[first.attribute]}
                            </Box>
                        </Typography>
                        <Typography variant="body2" gutterBottom>
                            {first.value || "\u2014"}
                        </Typography>
                    </Grid>
                    <Grid item xs={6}>
                        <Typography variant="subtitle1" component="div">
                            <Box component="span" fontWeight="fontWeightMedium">
                                {attributes[second.attribute]}
                            </Box>
                        </Typography>
                        <Typography variant="body2" gutterBottom>
                            {second.value || "\u2014"}
                        </Typography>
                    </Grid>
                </Grid>
            ))}
        </>
    );
};
