import React from "react";
import {
    List,
    ListItem,
    ListItemText,
    Link,
    Switch,
    Typography,
} from "@mui/material/";
import * as Sentry from "@sentry/react";
import makeStyles from "@mui/styles/makeStyles";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import { useDispatch, useSelector } from "react-redux";
import actions from "../Store/actions";
import styles from "./styles.js";
import { useToaster } from "../common/hooks";
import settingsItems from "./settingsItems";

const Settings = () => {
    const items = settingsItems();
    const classes = makeStyles(styles)();
    const { app } = useSelector((state) => state);
    const dispatch = useDispatch();
    const toast = useToaster();

    const onBackgroundSyncPress = () => {
        try {
            dispatch(
                actions.app.create.update({
                    backgroundSyncEnabled: !app.backgroundSyncEnabled,
                })
            );
        } catch (error) {
            console.error(error);
            toast.error(t("errors.backgroundSync"));
            Sentry.captureException(error);
        }
    };

    const linkColumms = (
        label: string,
        icon: Boolean,
        link: string,
        secondaryLabel: string,
        actions: Boolean
    ) => (
        <ListItem
            className={classes.listItem}
            key={`${label}_item`}
            secondaryAction={
                icon ? (
                    <ChevronRightIcon color="primary" />
                ) : (
                    <Typography type="body1">{secondaryLabel}</Typography>
                )
            }
        >
            <ListItemText
                primary={
                    actions ? (
                        <Typography color="primary">{label}</Typography>
                    ) : (
                        <Link
                            component="a"
                            rel="noopener noreferrer"
                            href={link === null ? "#" : link}
                            target={link === null ? "_self" : "_blank"}
                            underline={link === null ? "none" : "always"}
                        >
                            {label}
                        </Link>
                    )
                }
            />
            {actions ? (
                <Switch
                    edge="end"
                    defaultChecked
                    onChange={onBackgroundSyncPress}
                    inputProps={{ "aria-labelledby": "background-sync" }}
                />
            ) : null}
        </ListItem>
    );

    return (
        <>
            <List className={classes.settingsListContainer}>
                {items.map((item) =>
                    linkColumms(
                        item.label,
                        item.icon,
                        item.path,
                        item.secondaryText,
                        item.actions
                    )
                )}
            </List>
        </>
    );
};

export default Settings;
