import { all, call, put, takeLatest } from "redux-saga/effects";
import db from "../../common/lib/database";
import actions from "../actions";
import { actionCreators, actionTypes } from "./actions";
import { get, update } from "idb-keyval";

function* selectAssetAuditors(action) {
    const { assignments } = action.payload;
    try {
        let assetAuditors = yield call(get, db.types.assetAuditor);
        if (assetAuditors)
            assetAuditors = assetAuditors.filter((item) =>
                assignments.includes(item.auditAssignment.id)
            );

        yield put(actionCreators.selectSuccess(assetAuditors));
    } catch (error) {
        yield put(actionCreators.selectFailure(error.toString()));
    }
}

function* writeAssetAuditors(action) {
    const { assetAuditors } = action.payload;
    try {
        yield call(update, db.types.assetAuditor, (assetAuditorDB = []) => [
            ...assetAuditorDB,
            ...assetAuditors,
        ]);

        /** Update auditAsset db with isAssigned = true */

        const assetIds = assetAuditors.map((item) => item.auditAsset.id);

        yield call(update, db.types.auditAsset, (updatedAssetsDB = []) => {
            const updatedAssets = updatedAssetsDB.map((obj) => {
                if (assetIds.includes(obj.id)) obj.isAssigned = true;
                return obj;
            });
            return updatedAssets;
        });

        // update auditAssets store
        yield put(actions.auditAssets.create.setAssetAssigned(assetIds));
        yield put(actionCreators.writeSuccess(assetAuditors));
    } catch (error) {
        yield put(actionCreators.writeFailure(error.toString()));
    }
}

// for testing
export { selectAssetAuditors, writeAssetAuditors };

export default (function* () {
    yield all([
        takeLatest(
            actionTypes.ASSET_AUDITORS_SELECT_REQUEST,
            selectAssetAuditors
        ),
        takeLatest(
            actionTypes.ASSET_AUDITORS_WRITE_REQUEST,
            writeAssetAuditors
        ),
    ]);
})();
