import { all, call, put, select, takeLatest } from "redux-saga/effects";
import { uniqBy } from "lodash";
import { db } from "../../common/lib";
import actions from "../actions";
import { actionCreators, actionTypes } from "./actions";
import { get } from "idb-keyval";

function* selectAssignments() {
    try {
        const { auth } = yield select((state) => state);

        let auditAssignments =
            (yield call(get, db.types.auditAssignment)) || [];

        // extract locations that belong to logged-in user
        const auditLocations = auditAssignments.reduce((accum, item) => {
            if (item.auditor === auth.account.id)
                accum.push(item.auditLocation);

            return accum;
        }, []);

        const audits = auditLocations.map((item) => item.audit);
        const uniqueAudits = uniqBy(audits, "id");

        yield all([
            put(actionCreators.selectSuccess(auditAssignments)),
            put(actions.auditLocations.create.selectSuccess(auditLocations)),
            put(actions.audits.create.selectSuccess(uniqueAudits)),
        ]);
    } catch (error) {
        yield put(actionCreators.selectFailure(error.toString()));
    }
}

export default (function* () {
    yield all([
        takeLatest(
            actions.auditUpload.type.AUDIT_UPLOAD_POST_SUCCESS,
            selectAssignments
        ),
        takeLatest(
            actionTypes.AUDIT_ASSIGNMENTS_SELECT_REQUEST,
            selectAssignments
        ),
    ]);
})();
