import React, { useState } from "react";
import { useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import { Box, Button, IconButton, TextField } from "@mui/material/";
import makeStyles from "@mui/styles/makeStyles";
import { useParams, useNavigate } from "react-router-dom";
import { Outlet } from "react-router-dom";
import SearchIcon from "@mui/icons-material/Search";
import AddIcon from "@mui/icons-material/Add";
import styles from "./styles";
import SearchList from "./SearchList";
import useDebounceSearch from "./useDebounceSearch";
import useCombinedLocations from "./useCombinedLocations";

/** Params for search  */
const MIN_SEARCH_LENGTH = 3;

const Search = () => {
    const { t } = useTranslation();
    const navigate = useNavigate();
    const [searchText, setSearchText] = useState("");
    const { auditId } = useParams();
    const classes = makeStyles(styles)();
    const { auditAssets } = useSelector((state) => state);
    const combinedLocations = useCombinedLocations(parseInt(auditId));
    const otherAuditLocations = combinedLocations[1];

    const searchResult = useDebounceSearch(combinedLocations);
    const isLinked = (auditAsset: any) =>
        Boolean(
            otherAuditLocations.find(
                (item) => item.id === auditAsset.auditLocation.id
            )
        );

    const onSearchChange = (evt) => {
        const { value } = evt.target;
        setSearchText(value);
        if (searchText.length >= MIN_SEARCH_LENGTH) searchResult(value);
    };

    const onAddNewAssetPress = () => {
        navigate(`/auth/audits/${auditId}/new-asset`, {
            state: { searchText },
        });
    };

    const AddNewAsset = () => (
        <Box className={classes.addNewAsset} onClick={onAddNewAssetPress}>
            <Button startIcon={<AddIcon />}>{t("addNewAsset")}</Button>
        </Box>
    );

    return (
        <>
            <TextField
                fullWidth
                autoComplete="off"
                id="searchField"
                variant="outlined"
                type="search"
                label="Search"
                onChange={onSearchChange}
                value={searchText}
                InputProps={{
                    endAdornment: (
                        <IconButton>
                            <SearchIcon />
                        </IconButton>
                    ),
                }}
            />
            {searchText.length >= MIN_SEARCH_LENGTH ? (
                <Box elevation={2} className={classes.searchListContainer}>
                    {auditAssets.data.map((item) => (
                        <SearchList
                            searchText={searchText.toLowerCase()}
                            data={item}
                            isLinked={isLinked(item)}
                            key={`search-list-${item.id}`}
                        />
                    ))}
                    {auditAssets.data.length === 0 && <AddNewAsset />}
                </Box>
            ) : null}
            <Outlet />
        </>
    );
};

export default Search;
