import React, { useState, useEffect } from "react";
import { Box, Button, Tab } from "@mui/material/";
import { TabContext, TabList, TabPanel } from "@mui/lab/";
import { useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";
import * as Sentry from "@sentry/react";

import SearchIcon from "@mui/icons-material/Search";
import ListIcon from "@mui/icons-material/List";
import AuditCompletion from "./AuditCompletion";
import makeStyles from "@mui/styles/makeStyles";
import { useToaster } from "../common/hooks";

import actions from "../Store/actions";
import styles from "./styles";
import Assets from "./Assets";
import Search from "./Search";

const AuditAssets = () => {
    const { t } = useTranslation();
    const toast = useToaster();
    const [bottomTab, setBottomTab] = useState(t("assets"));
    const [assetTab, setAssetTab] = useState(t("unchecked"));
    const classes = makeStyles(styles)();
    const dispatch = useDispatch();

    useEffect(() => {
        try {
            dispatch(actions.assetSync.create.start());
            dispatch(actions.geoLocation.create.watchStart());
        } catch (error) {
            console.log(error);
            toast.error(t("errors.assetSyncGeoLocations"));
            Sentry.captureException(error);
        }

        return () => {
            dispatch(actions.assetSync.create.stop());
            dispatch(actions.geoLocation.create.watchStop());
        };
    }, []);

    const handleChange = (event, newValue) => {
        setBottomTab(newValue);
    };

    const onAssetChange = (name) => () => {
        const value = name.toLowerCase();
        setAssetTab(value);
    };

    return (
        <>
            <AuditCompletion />
            <Box className={classes.tabContainer}>
                {bottomTab === "assets" ? (
                    <Box className={classes.assetListButtonsContainer}>
                        <Button
                            className={`${classes.assetListButtons} ${
                                assetTab.toLowerCase() === "checked"
                                    ? classes.activeListButton
                                    : ""
                            }`}
                            onClick={onAssetChange("checked")}
                        >
                            Checked
                        </Button>
                        <Button
                            className={`${classes.assetListButtons} ${
                                assetTab.toLowerCase() === "unchecked"
                                    ? classes.activeListButton
                                    : ""
                            }`}
                            onClick={onAssetChange("unchecked")}
                        >
                            Unchecked
                        </Button>
                    </Box>
                ) : null}
                <TabContext value={bottomTab}>
                    <TabPanel
                        value={t("assets")}
                        className={classes.tabPanelContainer}
                    >
                        <Assets assetListType={assetTab}></Assets>
                    </TabPanel>
                    <TabPanel value={t("search")}>
                        <Search></Search>
                    </TabPanel>
                    <Box className={classes.bottomContainer}>
                        <TabList
                            onChange={handleChange}
                            aria-label="Bottom Navigation"
                        >
                            <Tab
                                icon={<ListIcon />}
                                label={t("assets")}
                                value={t("assets")}
                                className={`${classes.buttonTab} ${
                                    bottomTab.toLowerCase() === "assets"
                                        ? classes.activeBottomTab
                                        : null
                                }`}
                            />
                            <Tab
                                icon={<SearchIcon />}
                                label={t("search")}
                                value={t("search")}
                                className={`${classes.buttonTab} ${
                                    bottomTab.toLowerCase() === "search"
                                        ? classes.activeBottomTab
                                        : null
                                }`}
                            />
                        </TabList>
                    </Box>
                </TabContext>
            </Box>
        </>
    );
};

export default AuditAssets;
