import helpers from "../../common/lib/helpers";
import getInitialState from "../get-initial-state";
import { actionTypes } from "./actions";

const initialState = getInitialState.list();

export default function (state = initialState, action) {
    switch (action.type) {
        case actionTypes.AUDIT_LOCATIONS_SELECT_SUCCESS: {
            const { auditLocations } = action.payload;
            return helpers.list.getReducer(initialState, auditLocations);
        }
        default: {
            return state;
        }
    }
}
