import { ERROR_ACTION, WAIT_FOR_ACTION } from 'redux-wait-for-action';

export const actionTypes = {
  AUDIT_ITEM_CLEAR: 'AUDIT_ITEM_CLEAR',
  AUDIT_ITEM_SET: 'AUDIT_ITEM_SET',

  AUDIT_ITEM_SELECT_FAILURE: 'AUDIT_ITEM_SELECT_FAILURE',
  AUDIT_ITEM_SELECT_REQUEST: 'AUDIT_ITEM_SELECT_REQUEST',
  AUDIT_ITEM_SELECT_SUCCESS: 'AUDIT_ITEM_SELECT_SUCCESS',

  AUDITS_SELECT_FAILURE: 'AUDITS_SELECT_FAILURE',
  AUDITS_SELECT_REQUEST: 'AUDITS_SELECT_REQUEST',
  AUDITS_SELECT_SUCCESS: 'AUDITS_SELECT_SUCCESS',

  AUDITS_WRITE_FAILURE: 'AUDITS_WRITE_FAILURE',
  AUDITS_WRITE_REQUEST: 'AUDITS_WRITE_REQUEST',
  AUDITS_WRITE_SUCCESS: 'AUDITS_WRITE_SUCCESS',
};

export const actionCreators = {
  itemClear: () => ({
    type: actionTypes.AUDIT_ITEM_CLEAR,
  }),
  itemSet: (audit: any) => ({
    type: actionTypes.AUDIT_ITEM_SET,
    payload: { audit },
  }),

  itemSelectRequest: (query: string) => ({
    type: actionTypes.AUDIT_SELECT_REQUEST,
    payload: { query },
    [WAIT_FOR_ACTION]: actionTypes.AUDIT_SELECT_SUCCESS,
    [ERROR_ACTION]: actionTypes.AUDIT_SELECT_FAILURE,
  }),
  itemSelectSuccess: (audit: any) => ({
    type: actionTypes.AUDIT_SELECT_SUCCESS,
    payload: { audit },
  }),
  itemSelectFailure: (error: Error) => ({
    type: actionTypes.AUDIT_SELECT_FAILURE,
    error,
  }),

  selectRequest: (query: string) => ({
    type: actionTypes.AUDITS_SELECT_REQUEST,
    payload: { query },
    [WAIT_FOR_ACTION]: actionTypes.AUDITS_SELECT_SUCCESS,
    [ERROR_ACTION]: actionTypes.AUDITS_SELECT_FAILURE,
  }),
  selectSuccess: (audits: Array<any>) => ({
    type: actionTypes.AUDITS_SELECT_SUCCESS,
    payload: { audits },
  }),
  selectFailure: (error) => ({
    type: actionTypes.AUDITS_SELECT_FAILURE,
    error,
  }),

  writeRequest: (audits: Array<any>) => ({
    type: actionTypes.AUDITS_WRITE_REQUEST,
    payload: { audits },
    [WAIT_FOR_ACTION]: actionTypes.AUDITS_WRITE_SUCCESS,
    [ERROR_ACTION]: actionTypes.AUDITS_WRITE_FAILURE,
  }),
  writeSuccess: (audits: Array<any>) => ({
    type: actionTypes.AUDITS_WRITE_SUCCESS,
    payload: { audits },
  }),
  writeFailure: (error) => ({
    type: actionTypes.AUDITS_WRITE_FAILURE,
    error,
  }),
};

export default {
  create: actionCreators,
  type: actionTypes,
};
