import React, { useState } from "react";
import * as Sentry from "@sentry/react";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import {
    Grid,
    Box,
    Button,
    InputLabel,
    MenuItem,
    FormControl,
    TextField,
    Typography,
    Select,
} from "@mui/material/";
import { useNavigate, useParams } from "react-router-dom";
import { v4 as uuid } from "uuid";
import makeStyles from "@mui/styles/makeStyles";
import actions from "../Store/actions";

import { Camera, PhotoList, ActionBar } from "../common/components";
import { useAuditAsset, useToaster } from "../common/hooks";
import useAuditAssetLocations from "./useAuditAssetLocations";
import RemoveCircleOutlineIcon from "@mui/icons-material/RemoveCircleOutline";
import PhotoCameraIcon from "@mui/icons-material/PhotoCamera";
import styles from "./styles";

const defaultReason = {
    name: "",
    id: null,
};

const AuditAssetExclude = () => {
    const classes = makeStyles(styles)();
    const { t } = useTranslation();
    const { auditId, assetId } = useParams();
    const toast = useToaster();
    let navigate = useNavigate();
    const dispatch = useDispatch();
    const { auditAssets, auditAssignments, typesList } = useSelector(
        (state) => state
    );
    const asset = useAuditAsset(assetId);
    const [exclusionReason, setExclusionReason] = React.useState(defaultReason);
    const [note, setNote] = useState({
        content: "",
        createdDate: null,
        id: uuid(),
    });
    const [photos, setPhotos] = useState([]);
    const [loading, setLoading] = useState(false);
    const [camOpen, setCamOpen] = useState(false);
    const exclusionReasonTypes = typesList.exclusionReasonTypes.data;
    const excludeAsset = assetId ? true : false;

    useAuditAssetLocations(excludeAsset, auditId);

    const onExcludeClick = async () => {
        setLoading(true);
        try {
            const assetAuditor = {
                assignedDate: new Date().toISOString(),
                exclusionReason: exclusionReason.id,
                notes: note.content
                    ? [{ ...note, createdDate: new Date().toISOString() }]
                    : [],
            };
            const getAuditAssignment = (auditAsset: any) => {
                return auditAssignments.data.find(
                    (item) =>
                        item.auditLocation.id === auditAsset.auditLocation.id
                );
            };

            let data, url;
            if (!excludeAsset) {
                /** Excluding all asset */
                data = auditAssets.data.reduce((acc, item) => {
                    if (item.isAssigned === false) {
                        acc.push({
                            ...item,
                            ...assetAuditor,
                            auditAsset: {
                                id: item.id,
                                isAssigned: true,
                            },
                            auditImages: [] /**Images won't be captured many */,
                            auditAssignment: getAuditAssignment(item),
                            id: uuid(),
                        });
                    }
                    return acc;
                }, []);
                url = `/auth/audits/${auditId}/details`;
            } else {
                /** Excluding single asset */
                data = [
                    {
                        ...assetAuditor,
                        auditAsset: { id: parseInt(assetId), isAssigned: true },
                        auditAssignment: getAuditAssignment(asset),
                        auditImages: photos,
                        id: uuid(),
                    },
                ];
                url = `/auth/audits/${auditId}/assets/list`;
            }
            await dispatch(actions.assetAuditors.create.writeRequest(data));
            navigate(url);
        } catch (error) {
            Sentry.captureException(error);
            toast.error(t("errors.excludeAsset"));
            setLoading(false);
        }
    };

    const onExclusionReasonChange = (evt) => {
        const { value } = evt.target;
        setExclusionReason(value);
        setPhotos([]);
    };

    const onNoteChangeText = (evt) => {
        const { value } = evt.target;
        setNote({ ...note, content: value });
    };

    /** On Camera  */
    const onTakePhoto = () => {
        setCamOpen(true);
    };
    const onCamClose = () => {
        setCamOpen(false);
    };

    /** On Take Photo */
    const onPhotoSuccess = (photo: any) => {
        setPhotos([photo, ...photos]);
        onCamClose();
    };
    const onPhotoDelete = (photo: any) => {
        setPhotos(photos.filter(({ id }) => id !== photo.id));
    };
    const onPhotoChange = (photo: any) => {
        const next = photos.map((item) =>
            item.id === photo.id ? photo : item
        );
        setPhotos(next);
    };

    const generateButton = (label, disabledCheck, onClickFn, icon) => {
        return (
            <Grid item xs={6}>
                <Button
                    fullWidth
                    disabled={disabledCheck}
                    onClick={onClickFn}
                    variant={
                        label.toLowerCase() === "exclude"
                            ? "contained"
                            : "outlined"
                    }
                    endIcon={icon}
                >
                    {label}
                </Button>
            </Grid>
        );
    };

    /** Validations for buttons */
    const disableTakePhoto =
        !loading && exclusionReason.name.length < 1 ? true : false;
    const disableExclude =
        exclusionReason.name.length < 1 || note.content.length < 1
            ? true
            : false;

    return (
        <>
            <Box className={classes.excludeContainer}>
                <FormControl fullWidth className={classes.excludeTopContainer}>
                    <InputLabel id="exclusion-reason-select-label">
                        {t("exclusionReason")}
                    </InputLabel>
                    <Select
                        labelId="exclusion-reason-select-label"
                        id="exclusion-reason-select"
                        value={exclusionReason}
                        defaultValue={defaultReason}
                        label={t("exclusionReason")}
                        onChange={onExclusionReasonChange}
                    >
                        <MenuItem value={defaultReason}>
                            <em>None</em>
                        </MenuItem>
                        {exclusionReasonTypes.map((item, index) => (
                            <MenuItem value={item} key={index}>
                                {item.name}
                            </MenuItem>
                        ))}
                    </Select>
                </FormControl>
                <TextField
                    id="exclusion-reason-note"
                    label={`${t("addANote")}*`}
                    onChange={onNoteChangeText}
                    multiline
                    fullWidth
                    value={note.content}
                    rows={4}
                    placeholder={`${t("addANote")}*`}
                />
                <ActionBar>
                    <Grid container spacing={2}>
                        {generateButton(
                            t("takePhoto"),
                            disableTakePhoto,
                            onTakePhoto,
                            <PhotoCameraIcon />
                        )}
                        {generateButton(
                            t("exclude"),
                            disableExclude,
                            onExcludeClick,
                            <RemoveCircleOutlineIcon />
                        )}
                    </Grid>
                </ActionBar>
                {asset && camOpen && (
                    <Camera
                        assetTypeIds={[asset.assetCore.type]}
                        isVisible={camOpen}
                        lastPhoto={photos[0]}
                        onClose={onCamClose}
                        onSuccess={onPhotoSuccess}
                    />
                )}
                {asset && photos.length > 0 && (
                    <>
                        <Typography align="center" variant="h6">
                            {t("photos")}
                        </Typography>
                        <PhotoList
                            assetTypeId={asset.assetCore.type}
                            onChange={onPhotoChange}
                            onDelete={onPhotoDelete}
                            photos={photos}
                        />
                    </>
                )}
            </Box>
        </>
    );
};

export default AuditAssetExclude;
