import { ERROR_ACTION, WAIT_FOR_ACTION } from "redux-wait-for-action";

export const actionTypes = {
    ALT_ASSET_ITEM_CLEAR: "ALT_ASSET_ITEM_CLEAR",
    ALT_ASSET_ITEM_SET: "ALT_ASSET_ITEM_SET",
    ALT_ASSET_ITEM_UPDATE: "ALT_ASSET_ITEM_UPDATE",

    ALT_ASSETS_CLEAR: "ALT_ASSETS_CLEAR",

    ALT_ASSETS_SELECT_FAILURE: "ALT_ASSETS_SELECT_FAILURE",
    ALT_ASSETS_SELECT_REQUEST: "ALT_ASSETS_SELECT_REQUEST",
    ALT_ASSETS_SELECT_SUCCESS: "ALT_ASSETS_SELECT_SUCCESS",

    ALT_ASSETS_WRITE_FAILURE: "ALT_ASSETS_WRITE_FAILURE",
    ALT_ASSETS_WRITE_REQUEST: "ALT_ASSETS_WRITE_REQUEST",
    ALT_ASSETS_WRITE_SUCCESS: "ALT_ASSETS_WRITE_SUCCESS",
};

export const actionCreators = {
    clear: () => ({
        type: actionTypes.ALT_ASSETS_CLEAR,
    }),

    itemClear: () => ({
        type: actionTypes.ALT_ASSET_ITEM_CLEAR,
    }),
    itemSet: (altAsset: any) => ({
        type: actionTypes.ALT_ASSET_ITEM_SET,
        payload: { altAsset },
    }),
    itemUpdate: (altAsset: any) => ({
        type: actionTypes.ALT_ASSET_ITEM_UPDATE,
        payload: { altAsset },
    }),

    selectRequest: (auditId: number) => ({
        type: actionTypes.ALT_ASSETS_SELECT_REQUEST,
        payload: { auditId },
        [WAIT_FOR_ACTION]: actionTypes.ALT_ASSETS_SELECT_SUCCESS,
        [ERROR_ACTION]: actionTypes.ALT_ASSETS_SELECT_FAILURE,
    }),
    selectSuccess: (altAssets: Array<any>) => ({
        type: actionTypes.ALT_ASSETS_SELECT_SUCCESS,
        payload: { altAssets },
    }),
    selectFailure: (error) => ({
        type: actionTypes.ALT_ASSETS_SELECT_FAILURE,
        error,
    }),

    writeRequest: (altAssets: Array<any>) => ({
        type: actionTypes.ALT_ASSETS_WRITE_REQUEST,
        payload: { altAssets },
        [WAIT_FOR_ACTION]: actionTypes.ALT_ASSETS_WRITE_SUCCESS,
        [ERROR_ACTION]: actionTypes.ALT_ASSETS_WRITE_FAILURE,
    }),
    writeSuccess: (altAssets: Array<any>) => ({
        type: actionTypes.ALT_ASSETS_WRITE_SUCCESS,
        payload: { altAssets },
    }),
    writeFailure: (error) => ({
        type: actionTypes.ALT_ASSETS_WRITE_FAILURE,
        error,
    }),
};

export default {
    create: actionCreators,
    type: actionTypes,
};
