import React from "react";
import {
    Card,
    CardActions,
    CardContent,
    CardMedia,
    Typography,
    Button,
    IconButton,
} from "@mui/material/";
import { useNavigate, useParams } from "react-router-dom";
import makeStyles from "@mui/styles/makeStyles";
import RemoveCircleOutlineIcon from "@mui/icons-material/RemoveCircleOutline";
import AccessTimeIcon from "@mui/icons-material/AccessTime";
import DirectionsCarIcon from "@mui/icons-material/DirectionsCar";
import styles from "./styles.js";
import useTypesListMake from "../../common/hooks/useTypesListMake.js";
import useTypesListAssetTypes from "../../common/hooks/useTypesListAssetTypes.js";
type Props = {
    identifier1: string,
    identifier2: string,
    identifier3: string,
    identifier4: string,
    make: string,
    type: string,
    onDefer: Function,
    OnExclude: Function,
};
const AssetItem = ({
    assetId,
    assetType,
    auditImages,
    identifier1,
    identifier2,
    identifier3,
    identifier4,
    make,
    type,
    onDefer,
    onExclude,
}: Props) => {
    let navigate = useNavigate();
    const classes = makeStyles(styles)();
    const params = useParams();
    const { auditId } = params;
    const makeIdentifier = useTypesListMake(make);
    const typeIdentifier = useTypesListAssetTypes(type);

    const onCardClick = (assetId) => {
        navigate(`/auth/audits/${auditId}/assets/${assetId}/details`);
    };

    const generateCardText = (primaryText, variantType) => {
        return (
            <Typography
                gutterBottom
                variant={variantType === "body1" ? "body1" : "body2"}
                color="text.primary"
                align="left"
            >
                {primaryText}
            </Typography>
        );
    };

    const generateImage = (auditImages) => {
        if (auditImages && auditImages.length > 0) {
            return (
                <CardMedia
                    role="image"
                    className={classes.iconButton}
                    component="img"
                    image={auditImages[0].image}
                    alt={identifier1}
                />
            );
        } else {
            return (
                <IconButton className={classes.iconButton}>
                    <DirectionsCarIcon className={classes.icons} />
                </IconButton>
            );
        }
    };

    return (
        <Card raised={true} className={classes.cardContainer}>
            {assetType === "checked" ? generateImage(auditImages) : null}
            <CardContent
                className={classes.cardContent}
                onClick={() => {
                    assetType === "unchecked" ? onCardClick(assetId) : null;
                }}
            >
                {identifier1 && generateCardText(identifier1, "body1")}
                {identifier2 && generateCardText(identifier2)}
                {identifier3 && generateCardText(identifier3)}
                {identifier4 && generateCardText(identifier4)}
                {makeIdentifier.name && generateCardText(makeIdentifier.name)}
                {typeIdentifier.name && generateCardText(typeIdentifier.name)}
            </CardContent>

            {assetType === "unchecked" ? (
                <CardActions className={classes.cardActions}>
                    <Button
                        aria-label={`exclude-button-${assetId}`}
                        role="button"
                        className={`${classes.actionColButtons} ${classes.actionColFirstChild}`}
                        onClick={onExclude}
                    >
                        <RemoveCircleOutlineIcon color="error" />
                    </Button>
                    <Button
                        aria-label={`defer-button-${assetId}`}
                        role="button"
                        className={classes.actionColButtons}
                        onClick={onDefer}
                    >
                        <AccessTimeIcon color="info" />
                    </Button>
                </CardActions>
            ) : null}
        </Card>
    );
};

export default AssetItem;
