import React, { useState, useEffect } from "react";
import * as Sentry from "@sentry/react";
import makeStyles from "@mui/styles/makeStyles";
import { v4 as uuid } from "uuid";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams, useLocation } from "react-router-dom";
import actions from "../Store/actions";
import {
    useAssetTypeIdentifiers,
    useAssetTypeMakes,
    useAssetTypesForAuditOrganisation,
    useAssetTypeValidationRules,
    useDistinctAssetTypesForAudit,
    useDistinctMakesForAudit,
    useIsScrollingPresent,
    useAudit,
    useAuthenticated,
} from "../common/hooks";
import useMakes from "./useMakes";
import { useTranslation } from "react-i18next";
import { Camera, PhotoList, ActionBar } from "../common/components";
import {
    Box,
    TextField,
    Button,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    Grid,
} from "@mui/material/";
import CheckIcon from "@mui/icons-material/Check";
import AddAPhotoIcon from "@mui/icons-material/AddAPhoto";
import hdlIdentifierValidation from "./altAssetUtils";
import styles from "./styles";

const altAssetData = {
    audit: null,
    auditImages: [],
    identifier1: "",
    identifier2: "",
    identifier3: "",
    identifier4: "",
    make: 0,
    note: { content: "", createdDate: null },
    type: 0,
};

/**
 * Alt-asset Component
 */
export default () => {
    const classes = makeStyles(styles)();
    const { t } = useTranslation();
    const dispatch = useDispatch();
    const { auditId } = useParams();
    const location = useLocation();
    const navigate = useNavigate();

    const { invalidIdentifiers } = useSelector((state) => state.typesList);
    const [errors, setErrors] = useState({});
    const [loading, setLoading] = useState(false);
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [altAsset, setAltAsset] = useState(altAssetData);

    const isScrollingPresent = useIsScrollingPresent("alt-asset-form");
    const assetTypes = useAssetTypesForAuditOrganisation();
    const { user } = useAuthenticated();
    const distinctAssetTypes = useDistinctAssetTypesForAudit();
    const distinctMakes = useDistinctMakesForAudit(auditId);
    const assetTypeMakes = useAssetTypeMakes(altAsset.type);
    const audit = useAudit(auditId);
    const identifiers = useAssetTypeIdentifiers(altAsset.type);
    const rules = useAssetTypeValidationRules(altAsset.type);
    const makes = useMakes(assetTypeMakes, distinctMakes);

    useEffect(() => {
        if (distinctAssetTypes.length === 1) {
            setAltAsset({ ...altAssetData, type: distinctAssetTypes[0] });
        }
    }, [distinctAssetTypes]);

    // populate available identifiers with a search value
    useEffect(() => {
        if (location.state && location.state.searchText) {
            if (Object.keys(identifiers).length > 0) {
                const values = Object.keys(identifiers).reduce(
                    (aggregate, key) => ({
                        ...aggregate,
                        [key]: location.state.searchText,
                    }),
                    {}
                );
                const newAltAsset = { ...altAsset, ...values };
                setAltAsset(newAltAsset);
            }
        }
    }, [location, identifiers]);

    const handleChange = (name: string) => (event: any) => {
        const { value } = event.target;
        setAltAsset({ ...altAsset, [name]: value });

        // on typing remove errors if any
        if (errors[name]) {
            const newErrors = { ...errors };
            delete newErrors[name];
            setErrors(newErrors);
        }
    };

    const handleModalShow = () => setIsModalVisible(true);
    const onModalHide = () => setIsModalVisible(false);

    const makeIdentifer = (name: string) =>
        identifiers[name] ? (
            <TextField
                error={Boolean(errors[name])}
                id={name}
                label={t(name)}
                value={altAsset[name]}
                variant="outlined"
                helperText={errors[name]}
                placeholder={identifiers[name]}
                onChange={handleChange(name)}
                className={classes.margin}
                fullWidth
                inputProps={{
                    "data-testid": `alt-asset-${name}`,
                }}
            />
        ) : null;

    const onCheckAsset = async () => {
        // check if identifiers are correct
        const [hasValidationFailed, valErrors] = hdlIdentifierValidation(
            identifiers,
            altAsset,
            rules,
            invalidIdentifiers.data
        );

        if (hasValidationFailed) {
            setErrors({ ...errors, ...valErrors });
            return;
        }

        if (!audit) {
            dispatch(actions.toast.create.error(t("errors.checkAltAsset")));
            return;
        }

        setLoading(true);
        try {
            const {
                identifier1,
                identifier2,
                identifier3,
                identifier4,
                make,
                note,
                type,
                auditImages,
            } = altAsset;

            const newUUID = uuid();

            const newAltAsset = {
                identifier1,
                identifier2,
                identifier3,
                identifier4,
                audit,
                createdBy: user.account.id,
                createdDate: new Date().toISOString(),
                make,
                auditImages,
                notes: note.content
                    ? [
                          {
                              ...note,
                              createdDate: new Date().toISOString(),
                              id: uuid(),
                          },
                      ]
                    : [],
                type,
                // new asset does not have incremental db id, therefore
                // we temporarily generate it to avoid UI errors within reduce
                id: newUUID,
                uuid: newUUID,
            };

            await dispatch(
                actions.altAssets.create.writeRequest([newAltAsset])
            );
            setLoading(false);
            navigate(`/auth/audits/${auditId}/assets/list`);
        } catch (error) {
            console.log(error);
            Sentry.captureException(error);
            setLoading(false);
        }
    };

    const onNoteChangeText = (e: object) => {
        const { value } = e.target;
        const next = { ...altAsset.note, content: value };
        setAltAsset({ ...altAsset, note: next });
    };

    const onPhotoChange = (photo: any) => {
        const next = altAsset.auditImages.map((item) =>
            item.id === photo.id ? photo : item
        );
        setAltAsset({ ...altAsset, auditImages: next });
    };

    const onPhotoDelete = (photo: any) => {
        const next = altAsset.auditImages.filter(({ id }) => id !== photo.id);
        setAltAsset({ ...altAsset, auditImages: next });
    };

    const onPhotoSuccess = (photo: any) => {
        const next = [photo, ...altAsset.auditImages];
        setAltAsset({ ...altAsset, auditImages: next });
        onModalHide();
    };

    const onValidate = () => {
        if (Object.keys(errors).length > 0) {
            return false;
        }
        const { identifier1, identifier2, identifier3, identifier4 } = altAsset;
        if (!(identifier1 || identifier2 || identifier3 || identifier4)) {
            return false;
        } else if (altAsset.auditImages.length === 0) {
            return false;
        } else if (!(altAsset.make && altAsset.type)) {
            return false;
        }
        return true;
    };

    const hasPhotos = Boolean(altAsset && altAsset.auditImages.length);

    return (
        <>
            <Box
                id="alt-asset-form"
                component="form"
                noValidate
                autoComplete="off"
                className={classes.boxForm}
                style={{ height: isScrollingPresent ? "auto" : "100%" }}
            >
                <FormControl fullWidth className={classes.margin}>
                    <InputLabel id="asset-type-select-label">
                        {t("assetType")}
                    </InputLabel>
                    <Select
                        id="asset-type-select"
                        value={altAsset.type}
                        fullWidth
                        label={t("assetType")}
                        onChange={handleChange("type")}
                        data-testid={"asset-type-picker"}
                    >
                        <MenuItem value={0}>
                            <em>None</em>
                        </MenuItem>
                        {assetTypes.map((item, index) => (
                            <MenuItem value={item.id} key={index}>
                                {item.name}
                            </MenuItem>
                        ))}
                    </Select>
                </FormControl>

                {makeIdentifer("identifier2")}
                {makeIdentifer("identifier1")}
                {makeIdentifer("identifier3")}
                {makeIdentifer("identifier4")}

                <FormControl fullWidth className={classes.margin}>
                    <InputLabel id="make-select-label">
                        {t("make_one")}
                    </InputLabel>
                    <Select
                        id="make-select"
                        value={altAsset.make}
                        fullWidth
                        label={t("make_one")}
                        onChange={handleChange("make")}
                        data-testid={"make-picker"}
                    >
                        <MenuItem value={0}>
                            <em>None</em>
                        </MenuItem>
                        {makes.map((item, index) => (
                            <MenuItem value={item.id} key={index}>
                                {item.name}
                            </MenuItem>
                        ))}
                    </Select>
                </FormControl>

                {hasPhotos && (
                    <PhotoList
                        assetTypeId={altAsset.type}
                        onChange={onPhotoChange}
                        onDelete={onPhotoDelete}
                        photos={altAsset.auditImages}
                        className={classes.margin}
                    />
                )}

                <TextField
                    id="alt-asset-note"
                    label={t("addANote")}
                    multiline
                    fullWidth
                    onChange={onNoteChangeText}
                    placeholder={t("addANote")}
                    rows={4}
                    value={altAsset.note.content}
                    className={classes.margin}
                    inputProps={{
                        "data-testid": "add-alt-asset-note",
                    }}
                />
            </Box>
            <ActionBar>
                <Grid container spacing={2}>
                    <Grid item xs={6}>
                        <Button
                            variant="outlined"
                            color="primary"
                            disabled={loading || !altAsset.type}
                            fullWidth
                            onClick={handleModalShow}
                            endIcon={<AddAPhotoIcon />}
                        >
                            {t(hasPhotos ? "addPhoto" : "takePhoto")}
                        </Button>
                    </Grid>
                    <Grid item xs={6}>
                        <Button
                            variant="contained"
                            color="primary"
                            disabled={!onValidate()}
                            fullWidth
                            onClick={onCheckAsset}
                            endIcon={<CheckIcon />}
                        >
                            {t("checkAsset")}
                        </Button>
                    </Grid>
                </Grid>
            </ActionBar>
            <Camera
                assetTypeIds={[altAsset.type]}
                isVisible={isModalVisible}
                lastPhoto={altAsset.auditImages[0]}
                onClose={onModalHide}
                onSuccess={onPhotoSuccess}
            />
        </>
    );
};
