import { useCallback, useState } from "react";
import debounce from "lodash/debounce";
import { useDispatch } from "react-redux";

import actions from "../../Store/actions";

const DEBOUNCE_DELAY = 400;
export default (combinedLocations) => {
    const dispatch = useDispatch();

    const useDebounceSearch = useCallback(
        debounce((value: string) => {
            const auditAuditLocations = combinedLocations[0];
            const otherAuditLocations = combinedLocations[1];
            if (value.length >= 3) {
                const locIds = [
                    ...auditAuditLocations,
                    ...otherAuditLocations,
                ].map((item) => item.id);
                dispatch(
                    actions.auditAssets.create.selectRequest(
                        locIds,
                        "search",
                        value.toLowerCase()
                    )
                );
            } else if (value.length === 0) {
                dispatch(actions.auditAssets.create.clear());
            }
        }, DEBOUNCE_DELAY),
        [combinedLocations]
    );
    return useDebounceSearch;
};
