import { v4 as uuid } from "uuid";
import { actionTypes } from "./actions";

const appendToast = (message, state, style) => {
    return [...state, { message, style, uuid: uuid() }];
};

export default function (state = [], action) {
    switch (action.type) {
        case actionTypes.TOAST_ERROR: {
            const { message } = action.payload;
            return appendToast(message, state, "error");
        }
        case actionTypes.TOAST_INFO: {
            const { message } = action.payload;
            return appendToast(message, state, "info");
        }
        case actionTypes.TOAST_MESSAGE: {
            const { message, style } = action.payload;
            return appendToast(message, state, style);
        }
        case actionTypes.TOAST_REMOVE: {
            // const { toast } = action.payload;
            // return state.filter(({ uuid }) => toast.uuid !== uuid);
            return (state = []);
        }
        case actionTypes.TOAST_SUCCESS: {
            const { message } = action.payload;
            return appendToast(message, state, "success");
        }
        case actionTypes.TOAST_WARNING: {
            const { message } = action.payload;
            return appendToast(message, state, "warning");
        }
        default: {
            return state;
        }
    }
}
