import getInitialState from "../get-initial-state";
import { actionTypes } from "./actions";

const initialState = {
    assetCategories: getInitialState.list(),
    assetTypes: getInitialState.list(),
    auditAssignmentStatus: getInitialState.list(),
    auditStatus: getInitialState.list(),
    exclusionImageTypes: getInitialState.list(),
    exclusionReasonTypes: getInitialState.list(),
    fileGroups: getInitialState.list(),
    fileMappings: getInitialState.list(),
    fileTypes: getInitialState.list(),
    groupAuditStatus: getInitialState.list(),
    identifiers: getInitialState.list(),
    imageTypeIdentifiers: getInitialState.list(),
    imageTypes: getInitialState.list(),
    makes: getInitialState.list(),
    makeTypes: getInitialState.list(),
    organisationTypes: getInitialState.list(),
    typeAttributes: getInitialState.list(),
    typeIdentifiers: getInitialState.list(),
    uploadStatus: getInitialState.list(),
    validationFields: getInitialState.list(),
    validationRules: getInitialState.list(),
};

export default function (state = initialState, action) {
    switch (action.type) {
        case actionTypes.TYPES_LIST_GET_SUCCESS: {
            const { typesList } = action.payload;
            return typesList;
        }
        default: {
            return state;
        }
    }
}
