import { all, call, put, select, takeLatest } from "redux-saga/effects";
import { actionCreators, actionTypes } from "./actions";
import { get } from "idb-keyval";
import { db, helpers } from "../../common/lib/";

function* selectRequestFromDb(action) {
    const { locIds, requestType, search } = action.payload;
    try {
        let results = (yield call(get, db.types.auditAsset)) || [];
        results = db.queries.forAuditAssets(
            locIds,
            requestType,
            search,
            results
        );
        yield put(actionCreators.selectSuccess(results));
    } catch (error) {
        yield put(actionCreators.selectFailure(error.toString()));
    }
}

function* writeRequestToDb(action) {
    const { auditAssets } = action.payload;
    try {
        yield put(actionCreators.writeSuccess(auditAssets));
    } catch (error) {
        yield put(actionCreators.writeFailure(error.toString()));
    }
}

export default (function* () {
    yield all([
        takeLatest(
            actionTypes.AUDIT_ASSETS_SELECT_REQUEST,
            selectRequestFromDb
        ),
        takeLatest(actionTypes.AUDIT_ASSETS_WRITE_REQUEST, writeRequestToDb),
    ]);
})();
