import React from "react";
import { useParams } from "react-router-dom";
import { useSelector } from "react-redux";
import CircleIcon from "@mui/icons-material/Circle";
import { useTranslation } from "react-i18next";
import { Chip, Grid, Box, Typography } from "@mui/material/";
import makeStyles from "@mui/styles/makeStyles";
import { useAuditProgressDetails } from "../common/hooks";
import { colors } from "../common/styles";
import styles from "./styles";

import CheckIcon from "@mui/icons-material/Check";
import RotateLeftIcon from "@mui/icons-material/RotateLeft";
import ErrorOutlineIcon from "@mui/icons-material/ErrorOutline";

/**
 * Audit Completion Component
 */
export default () => {
    const { auditId } = useParams();
    const { green, grey } = colors;
    const { t } = useTranslation();
    const classes = makeStyles(styles)();
    const { app } = useSelector((state) => state);
    const { alt, assigned, required } = useAuditProgressDetails(
        "store",
        auditId
    );
    const { netConnected } = app;

    return (
        <Box className={classes.assetListHeading}>
            <Grid container>
                <Grid item xs={6}>
                    <Chip
                        className={classes.chip}
                        icon={<CheckIcon fontSize="small" />}
                        label={assigned}
                    />
                    <Chip
                        className={classes.chip}
                        icon={<RotateLeftIcon fontSize="small" />}
                        label={required - assigned}
                    />
                    <Chip
                        className={classes.chip}
                        icon={<ErrorOutlineIcon fontSize="small" />}
                        label={alt}
                    />
                </Grid>
                <Grid item xs={6} className={classes.alignGrid}>
                    <Box className={classes.loadingState}>
                        <Typography
                            variant="body2"
                            component="span"
                            className={classes.netConnected}
                        >
                            {netConnected ? t("online") : t("offline")}
                        </Typography>
                        <CircleIcon
                            fontSize="small"
                            sx={{ color: netConnected ? green : grey }}
                        />
                    </Box>
                </Grid>
            </Grid>
        </Box>
    );
};
