import { all, call, put, takeLatest } from "redux-saga/effects";
import { db } from "../../common/lib";
import { actionCreators, actionTypes } from "./actions";
import { get } from "idb-keyval";

function* select(action) {
    const { query } = action.payload;
    try {
        const auditLocations = yield call(get, db.types.auditLocation);
        const results = auditLocations.filtered(query);

        yield put(actionCreators.selectSuccess(results));
    } catch (error) {
        yield put(actionCreators.selectFailure(error.toString()));
    }
}

export default (function* () {
    yield all([takeLatest(actionTypes.AUDIT_LOCATIONS_SELECT_REQUEST, select)]);
})();
