import React, { useEffect } from "react";
import * as Sentry from "@sentry/react";
import { useDispatch, useSelector } from "react-redux";
import actions from "../Store/actions";
import { Outlet, useParams } from "react-router-dom";
import makeStyles from "@mui/styles/makeStyles";
import styles from "./styles";
import { useTranslation } from "react-i18next";
import { useToaster } from "../common/hooks";
import { Box, CircularProgress } from "@mui/material/";

const AuditAsset = () => {
    const dispatch = useDispatch();
    const { auditId } = useParams();
    const classes = makeStyles(styles)();
    const { t } = useTranslation();
    const toast = useToaster();

    const { audits, auditAssets, auditAssignments } = useSelector(
        (state) => state
    );

    // TODO to remove, load audit asset
    useEffect(async () => {
        await dispatch(actions.auditAssignments.create.selectRequest());
    }, []);

    useEffect(async () => {
        if (!auditAssignments) return;

        try {
            const assignments = auditAssignments.data.filter(
                (item) => item.auditLocation.audit.id === parseInt(auditId)
            );

            const locIds = assignments.map((item) => item.auditLocation.id);

            await dispatch(actions.auditAssets.create.selectRequest(locIds));
        } catch (error) {
            console.log(error);
            toast.error(t("errors.auditAssetSelect"));
            Sentry.captureException(error);
        }
    }, [auditAssignments, audits]);

    return (
        <>
            {auditAssets.data.length > 0 ? (
                <Outlet />
            ) : (
                <Box className={classes.box}>
                    <CircularProgress />
                </Box>
            )}
        </>
    );
};

export default AuditAsset;
