import { ERROR_ACTION, WAIT_FOR_ACTION } from "redux-wait-for-action";

export const actionTypes = {
    ASSET_AUDITORS_SELECT_FAILURE: "ASSET_AUDITORS_SELECT_FAILURE",
    ASSET_AUDITORS_SELECT_REQUEST: "ASSET_AUDITORS_SELECT_REQUEST",
    ASSET_AUDITORS_SELECT_SUCCESS: "ASSET_AUDITORS_SELECT_SUCCESS",

    ASSET_AUDITORS_WRITE_FAILURE: "ASSET_AUDITORS_WRITE_FAILURE",
    ASSET_AUDITORS_WRITE_REQUEST: "ASSET_AUDITORS_WRITE_REQUEST",
    ASSET_AUDITORS_WRITE_SUCCESS: "ASSET_AUDITORS_WRITE_SUCCESS",
};

export const actionCreators = {
    selectRequest: (assignments) => ({
        type: actionTypes.ASSET_AUDITORS_SELECT_REQUEST,
        payload: { assignments },
        [WAIT_FOR_ACTION]: actionTypes.ASSET_AUDITORS_SELECT_SUCCESS,
        [ERROR_ACTION]: actionTypes.ASSET_AUDITORS_SELECT_FAILURE,
    }),
    selectSuccess: (assetAuditors: Array<any>) => ({
        type: actionTypes.ASSET_AUDITORS_SELECT_SUCCESS,
        payload: { assetAuditors },
    }),
    selectFailure: (error: Error) => ({
        type: actionTypes.ASSET_AUDITORS_SELECT_FAILURE,
        error,
    }),

    writeRequest: (assetAuditors: Array<any>) => ({
        type: actionTypes.ASSET_AUDITORS_WRITE_REQUEST,
        payload: { assetAuditors },
        [WAIT_FOR_ACTION]: actionTypes.ASSET_AUDITORS_WRITE_SUCCESS,
        [ERROR_ACTION]: actionTypes.ASSET_AUDITORS_WRITE_FAILURE,
    }),
    writeSuccess: (assetAuditors: Array<any>) => ({
        type: actionTypes.ASSET_AUDITORS_WRITE_SUCCESS,
        payload: { assetAuditors },
    }),
    writeFailure: (error: Error) => ({
        type: actionTypes.ASSET_AUDITORS_WRITE_FAILURE,
        error,
    }),
};

export default {
    create: actionCreators,
    type: actionTypes,
};
