export const actionTypes = {
  TOAST_ERROR: 'TOAST_ERROR',
  TOAST_INFO: 'TOAST_INFO',
  TOAST_MESSAGE: 'TOAST_MESSAGE',
  TOAST_REMOVE: 'TOAST_REMOVE',
  TOAST_SUCCESS: 'TOAST_SUCCESS',
  TOAST_WARNING: 'TOAST_WARNING',
};

export const actionCreators = {
  error: (message: string) => ({
    type: actionTypes.TOAST_ERROR,
    payload: { message },
  }),
  info: (message: string) => ({
    type: actionTypes.TOAST_INFO,
    payload: { message },
  }),
  message: (message: string, style: any) => ({
    type: actionTypes.TOAST_MESSAGE,
    payload: { message, style },
  }),
  remove: (toast: any) => ({
    type: actionTypes.TOAST_REMOVE,
    payload: { toast },
  }),
  success: (message: string) => ({
    type: actionTypes.TOAST_SUCCESS,
    payload: { message },
  }),
  warning: (message: string) => ({
    type: actionTypes.TOAST_WARNING,
    payload: { message },
  }),
};

export default {
  create: actionCreators,
  type: actionTypes,
};
