import React from "react";
import { useDispatch } from "react-redux";
import makeStyles from "@mui/styles/makeStyles";
import actions from "../../Store/actions";
import AssetItem from "./AssetItem";
import { useNavigate, useParams } from "react-router-dom";
import * as Sentry from "@sentry/react";
import { useTranslation } from "react-i18next";
import { useToaster } from "../../common/hooks";
import styles from "./styles";

const Unchecked = ({ data }) => {
    const classes = makeStyles(styles)();
    const dispatch = useDispatch();
    const params = useParams();
    const { auditId } = params;
    const { t } = useTranslation();
    const toast = useToaster();

    let navigate = useNavigate();

    const onDeferClick = (auditAsset) => () => {
        try {
            const updAuditAsset = {
                ...auditAsset,
                deferredTimestamp: Date.now(),
            };
            dispatch(actions.auditAssets.create.writeRequest([updAuditAsset]));
        } catch (error) {
            console.log(error);
            toast.error(t("errors.deferAsset"));
            Sentry.captureException(error);
        }
    };

    const onExcludeClick = (auditAsset: any) => () => {
        try {
            const assetId = auditAsset.id;
            dispatch(actions.auditAssets.create.itemSet(auditAsset));
            navigate(`/auth/audits/${auditId}/assets/${assetId}/exclude`);
        } catch (error) {
            console.log(error);
            toast.error(t("errors.excludeAsset"));
            Sentry.captureException(error);
        }
    };

    return (
        <div className={classes.listContainer}>
            {data.map((item) => (
                <AssetItem
                    assetType="unchecked"
                    key={`dataUnchecked_${item.id}`}
                    assetId={item.id}
                    {...item.assetCore}
                    onDefer={onDeferClick(item)}
                    onExclude={onExcludeClick(item)}
                />
            ))}
        </div>
    );
};

export default Unchecked;
