import { all, call, put, select, takeLatest } from "redux-saga/effects";
import { chunk } from "lodash";
import db from "../../common/lib/database";
import { enums, http } from "../../common/lib";
import config from "../../config";
import { get } from "idb-keyval";
import { store } from "..";
import actions from "../actions";
import { actionCreators, actionTypes } from "./actions";
import sagaUtils from "./sagaUtils.js";

const BATCH_SIZE = 5;

function* post() {
    try {
        yield put(actionCreators.statusInit());

        const { audits } = yield select((state) => state);
        const { cleanAllData, mapAltAsset, mapAssetAuditor } = sagaUtils;
        const altAssetsDB = (yield call(get, db.types.altAsset)) || [];
        const altAssets = db.queries.forAudit(audits.item.id, altAssetsDB);
        const altAssetsToSubmit = altAssets.filter((item) =>
            Boolean(item.uuid)
        );

        for (let batch of chunk(altAssetsToSubmit, BATCH_SIZE)) {
            /** Alt asset   construction to post */
            const content = yield all(
                batch.map((item) => call(mapAltAsset, item))
            );

            yield call(
                http.authorized.post,
                config.api.paths.auditSubmissions,
                {
                    createdDate: new Date().toISOString(),
                    altAssetContent: content,
                    audit: audits.item.id,
                }
            );
            yield put(actionCreators.statusUploaded(batch.length));
        }

        /** To map assetAuditor we need auditAssignment  */
        const auditAssignmentDB =
            (yield call(get, db.types.auditAssignment)) || [];
        const auditAssignments = db.queries.forAuditLocationAudit(
            audits.item.id,
            auditAssignmentDB
        );

        /** Retriving the asset auditor Database  */
        const assetAuditorsDB = (yield call(get, db.types.assetAuditor)) || [];
        for (let auditAssignment of auditAssignments) {
            /** Filtering required daata from assetAAuditor */
            const assetAuditors = db.queries.forAuditAssignments(
                [auditAssignment.id],
                assetAuditorsDB
            );

            if (assetAuditors.length === 0) {
                yield call(
                    http.authorized.post,
                    config.api.paths.auditSubmissions,
                    {
                        createdDate: new Date().toISOString(),
                        audit: audits.item.id,
                        auditAssetContent: [],
                        auditAssignment: auditAssignment.id,
                    }
                );
            } else
                for (let batch of chunk(assetAuditors, BATCH_SIZE)) {
                    /** Asset auditor construction to post */
                    const content = yield all(
                        batch.map((item) => call(mapAssetAuditor, item))
                    );

                    yield call(
                        http.authorized.post,
                        config.api.paths.auditSubmissions,
                        {
                            createdDate: new Date().toISOString(),
                            audit: audits.item.id,
                            auditAssetContent: content,
                            auditAssignment: auditAssignment.id,
                        }
                    );
                    yield put(actionCreators.statusUploaded(batch.length));
                }
            /** Audit Assignment status to be submitted */
            yield call(
                http.authorized.post,
                config.api.paths.auditAssignmentBulkUpdate,
                [
                    {
                        id: auditAssignment.id,
                        status: enums.auditAssignment.submitted,
                    },
                ]
            );
        }

        // Clearing the data and post success message and clearing current audit
        yield call(cleanAllData, audits.item);
        store.dispatch(actions.auditsStarted.create.clear());
        yield put(actionCreators.postSuccess());
    } catch (error) {
        yield put(actionCreators.postFailure(error.toString()));
    }
}

/** Status of upladed data */
function* statusInit() {
    try {
        const { audits } = yield select((state) => state);
        /* Get auditAssignment  */
        const auditAssignmentDB =
            (yield call(get, db.types.auditAssignment)) || [];
        const auditAssignments = db.queries.forAuditLocationAudit(
            audits.item.id,
            auditAssignmentDB
        );

        /* Get asetAuditors**/
        const assignmentIds = auditAssignments.map((item) => item.id);
        const assetAuditorsDB = (yield call(get, db.types.assetAuditor)) || [];
        const assetAuditors = db.queries.forAuditAssignments(
            assignmentIds,
            assetAuditorsDB
        );

        /* Get altAssets**/
        const altAssetsDB = (yield call(get, db.types.altAsset)) || [];
        const altAssets = db.queries.forAudit(audits.item.id, altAssetsDB);

        yield put(
            actionCreators.statusSet({
                total: altAssets.length + assetAuditors.length,
                uploaded: 0,
            })
        );
    } catch (error) {
        yield put(actions.toast.create.error("messages.auditUploadStatusInit"));
    }
}

// For testing
export { post, statusInit };
export default (function* () {
    yield all([
        takeLatest(actionTypes.AUDIT_UPLOAD_POST_REQUEST, post),
        takeLatest(actionTypes.AUDIT_UPLOAD_STATUS_INIT, statusInit),
    ]);
})();
