import helpers from "../../common/lib/helpers";
import getInitialState from "../get-initial-state";
import { actionTypes } from "./actions";

const initialState = getInitialState.list();

export default function (state = initialState, action) {
    switch (action.type) {
        case actionTypes.ASSET_AUDITORS_SELECT_SUCCESS: {
            const { assetAuditors } = action.payload;
            return helpers.list.getReducer(state, assetAuditors);
        }
        case actionTypes.ASSET_AUDITORS_WRITE_SUCCESS: {
            const { assetAuditors } = action.payload;
            return helpers.list.mergeReducer(state, assetAuditors);
        }
        default: {
            return state;
        }
    }
}
