import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import * as Sentry from "@sentry/react";
import actions from "../Store/actions";
import { useNavigate } from "react-router-dom";
import {
    Box,
    CircularProgress,
    List,
    ListItem,
    ListItemText,
    Link,
    IconButton,
    Typography,
} from "@mui/material/";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import { useToaster } from "../common/hooks";
import makeStyles from "@mui/styles/makeStyles";
import { useTranslation } from "react-i18next";
import styles from "./styles.js";

const Audits = () => {
    let navigate = useNavigate();
    const { t } = useTranslation();
    const [loading, setLoading] = useState(true);
    const { audits } = useSelector((state) => state);
    const toast = useToaster();
    const dispatch = useDispatch();
    const classes = makeStyles(styles)();
    const { data } = audits;

    const onRefresh = async () => {
        setLoading(true);

        try {
            await dispatch(actions.typesList.create.getRequest());
            const { audits } = await dispatch(
                actions.auditDownload.create.getRequest()
            );
            toast.info(
                t("messages.auditsDownloadSuccess", {
                    count: audits ? audits.length : 0,
                })
            );
        } catch (error) {
            console.log(error);
            toast.error(t("errors.auditDownloadGet"));
            Sentry.captureException(error);
        }

        await dispatch(actions.auditAssignments.create.selectRequest());
        setLoading(false);
    };

    useEffect(() => {
        onRefresh();
    }, []);

    const handleAuditItemPress = (audit: any) => () => {
        const { id } = audit;

        try {
            dispatch(actions.audits.create.itemSet(audit));
        } catch (error) {
            console.error(error);
            toast.error(t("errors.auditItemSelection"));
            Sentry.captureException(error);
        }
        navigate(`/auth/audits/${id}/details`);
    };

    const generateList = (audit: any) => {
        const { name, id, date } = audit;

        return (
            <ListItem
                key={`${name}_${date}`}
                className={classes.listItem}
                onClick={handleAuditItemPress(audit)}
                secondaryAction={
                    <IconButton edge="end" aria-label="right" color="primary">
                        <ChevronRightIcon />
                    </IconButton>
                }
            >
                <ListItemText
                    primary={
                        <Link
                            component="a"
                            underline="none"
                            onClick={(e) => {
                                handleAuditItemPress(id);
                            }}
                        >
                            {name}
                        </Link>
                    }
                    secondary={date}
                />
            </ListItem>
        );
    };

    if (loading) {
        return (
            <Box className={classes.spinnerContainer}>
                <CircularProgress color="primary" />
                <Typography
                    color="primary"
                    component="p"
                    className={classes.loadingText}
                >
                    {t("messages.auditsBeingLoaded")}
                </Typography>
            </Box>
        );
    }

    return (
        <>
            {!loading && data.length > 0 ? (
                <List sx={{ width: "100%" }}>
                    {data.map((item) => generateList(item))}
                </List>
            ) : (
                <Box className={classes.messageContainer} onClick={onRefresh}>
                    <Typography component="p">
                        {t("messages.noPendingAudits")}
                    </Typography>
                    <Typography component="p">
                        {t("messages.tapToRefresh")}
                    </Typography>
                </Box>
            )}
        </>
    );
};

export default Audits;
