import React, { useState, useMemo, useEffect } from "react";
import {
    Typography,
    Modal,
    Button,
    Box,
    Container,
    CircularProgress,
    List,
    Grid,
} from "@mui/material/";
import * as Sentry from "@sentry/react";
import { useNavigate, useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import actions from "../Store/actions";
import { format } from "../common/lib";
import makeStyles from "@mui/styles/makeStyles";
import styles from "./styles.js";
import RoomIcon from "@mui/icons-material/Room";
import EventIcon from "@mui/icons-material/Event";
import { useTranslation } from "react-i18next";
import ListDetail from "./ListDetail";
import { ActionBar } from "../common/components";
import { useAudit, useToaster, useAuditProgressDetails } from "../common/hooks";
import AuditProgress from "./AuditProgress";

const AuditDetails = () => {
    let navigate = useNavigate();
    const { t } = useTranslation();
    const classes = makeStyles(styles)();
    const dispatch = useDispatch();
    const [loading, setLoading] = useState(false);

    const [messageIsShown, setMessageIsShown] = useState(false);
    const showMessage = () => setMessageIsShown(true);
    const hideMessage = () => setMessageIsShown(false);

    const { audits, auditLocations } = useSelector((state) => state);
    const { auditId } = useParams();
    const audit = useAudit(auditId);
    const toast = useToaster();

    const getLocations = () => {
        return auditLocations.data
            .filter((item) => item.audit.id === parseInt(auditId))
            .map((item) => item.location);
    };

    const locations = useMemo(getLocations, [auditLocations]);

    const { isComplete, alt, assigned, required } = useAuditProgressDetails(
        "db",
        auditId
    );
    const auditStarted = required - assigned === required ? false : true;
    const singleButtonLayout =
        auditStarted === true && isComplete === false ? false : true;

    useEffect(() => {
        setLoading(false);
        dispatch(actions.auditCompletion.create.init(auditId));
    }, [audits]);

    const createButton = (
        clickFn: Function,
        loading: Boolean,
        title: string,
        variantType: Boolean
    ) => (
        <Grid item xs={!singleButtonLayout ? 6 : 12}>
            <Button
                disabled={loading}
                fullWidth
                variant={variantType === false ? "outlined" : "contained"}
                loading={loading.toString()}
                onClick={clickFn}
                className={`${classes.buttons} 
               ${!singleButtonLayout ? classes.smallFontSize : ""}`}
            >
                {title}
            </Button>
        </Grid>
    );

    const onSubmitPress = async () => {
        navigate(`/auth/audits/${auditId}/submit`);
    };

    const onStartContinuePress = async () => {
        const { id } = audit;

        try {
            showMessage();
            setLoading(true);
            await dispatch(actions.geoLocation.create.getRequest());
        } catch (error) {
            console.log(error);
            toast.error(t("errors.geoLocationGet"));
            Sentry.captureException(error);
        }
        hideMessage();
        setLoading(false);
        navigate(`/auth/audits/${id}/assets/list`);
    };

    if (!audit)
        return (
            <Box className={classes.spinnerContainer}>
                <CircularProgress />
            </Box>
        );

    return (
        <>
            <Container className={classes.auditContainer}>
                {audit && (
                    <>
                        <List className={classes.listContainer}>
                            <ListDetail
                                icon={<RoomIcon color="primary" />}
                                primaryText={audit.branch.name}
                                data={locations}
                            />
                            <ListDetail
                                icon={<EventIcon color="primary" />}
                                primaryText={`Due: ${format.dateTime(
                                    audit.endDate
                                )}`}
                            />
                        </List>
                        <AuditProgress
                            alt={alt}
                            assigned={assigned}
                            required={required}
                        />
                        <ActionBar>
                            <Grid container spacing={2}>
                                {auditStarted &&
                                    createButton(
                                        onSubmitPress,
                                        loading,
                                        t(
                                            isComplete
                                                ? "submitAudit"
                                                : "submitPartialAudit"
                                        ),
                                        isComplete
                                    )}
                                {!isComplete &&
                                    createButton(
                                        onStartContinuePress,
                                        loading,
                                        t(
                                            auditStarted
                                                ? "continueAudit"
                                                : "startAudit"
                                        )
                                    )}
                            </Grid>
                        </ActionBar>
                    </>
                )}
                <Modal
                    open={messageIsShown}
                    onClose={hideMessage}
                    aria-labelledby="message-modal"
                    aria-describedby="preparing-audit-modal"
                >
                    <Box className={classes.modalBox}>
                        <Typography
                            id="modal-modal-description"
                            className={classes.progress}
                        >
                            {t("messages.preparingAudit")}
                        </Typography>
                        <CircularProgress />
                    </Box>
                </Modal>
            </Container>
        </>
    );
};

export default AuditDetails;
