import React from "react";
import { Box, Typography } from "@mui/material/";
import makeStyles from "@mui/styles/makeStyles";
import { useNavigate, useParams } from "react-router-dom";
import colors from "../../common/styles/colors";
import styles from "./styles";
import AssetStatusIcon from "./AssetStatusIcon";
import { useTypesListMake } from "../../common/hooks";

const { darkGreyText } = colors;

const SearchList = ({ data, searchText, isLinked }) => {
    let navigate = useNavigate();
    const params = useParams();
    const classes = makeStyles(styles)();
    const { assetCore, id, isAssigned } = data;

    const { identifier1, identifier2, identifier3, identifier4, make } =
        assetCore;
    const makeIdentifier = useTypesListMake(make);
    const { auditId } = params;

    /** If the variantType is present text will have bigger font szie */
    const getParagraph = (children, variantType) => (
        <Typography
            component="p"
            variant={variantType === "body1" ? "body1" : "body2"}
            color={variantType === "body1" ? "text.primary" : darkGreyText}
            align="left"
        >
            {children}
        </Typography>
    );

    const generateText = (primaryText, variantType) => {
        const startIndex = primaryText.toLowerCase().indexOf(searchText);
        if (startIndex < 0) {
            return getParagraph(primaryText, variantType);
        }
        const endIndex = startIndex + searchText.length;
        const text = (
            <>
                {primaryText.substring(0, startIndex)}
                {
                    <Typography component="span" className={classes.searchText}>
                        {primaryText.substring(startIndex, endIndex)}
                    </Typography>
                }
                {primaryText.substring(endIndex)}
            </>
        );
        return getParagraph(text);
    };

    const onSearchListClick = () => {
        navigate(`/auth/audits/${auditId}/assets/${id}/details`, {
            state: { linked: isLinked },
        });
    };

    return (
        <Box
            className={classes.searchList}
            onClick={isAssigned !== true ? onSearchListClick : null}
            role="list"
        >
            <Box className={classes.textBox}>
                {identifier1 && generateText(identifier1, "body1")}
                {identifier2 && generateText(identifier2)}
                {identifier3 && generateText(identifier3)}
                {identifier4 && generateText(identifier4)}

                {makeIdentifier.name && generateText(makeIdentifier.name)}
            </Box>
            <AssetStatusIcon data={data} isLinked={isLinked}></AssetStatusIcon>
        </Box>
    );
};
export default SearchList;
